.slider,
.product-slider,
.product-slider2{
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.25em;
      height: auto;
    }
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    position: static;
    margin: 0;
    @include lastM(0 .625em 0 0);
  }
  &._top{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: 2.5em;
  }
}

.swiper-button{
  box-shadow: 0 .25em 1.5625em rgba(0, 0, 0, 0.05);
  border: 1px solid #f3f4f8;
  width: 2.375em;
  height: 2.375em;
  border-radius: 50%;
  @include center;
  background-color: $white;
  &:hover{
    background-color: $primary;
    path{
      fill: $white;
    }
  }
  &.swiper-button-disabled{
    background-color: #e0e0e0;
    path{
      fill: #9ca1ac;
    }
  }
}

.swiper-pagination{
  position: static;
  margin-top: 1.875em;
  @include center;
  &-bullet{
    background: #5763c3;
    opacity: 0.4;
    width: .4375em;
    height: .4375em;
    margin: 0 .5em !important;
    border-radius: 31.25em;
    &-active{
      opacity: 1;
      width: 1.4375em;
    }
    &:first-child{
      margin-left: 0;
    }
    &:last-child{
      margin-right: 0;
    }
  }
}

.main{
  background-color: $light-grey;
  padding: 1.25em 0;
  overflow: hidden;
  &__inner{
    position: relative;
  }
}

.main-slider{
  width: calc(100% - 22.5em);
  max-width: 100%;
  border-radius: .375em;
  overflow: hidden;
  position: relative;
  .swiper{
    &-nav{
      position: absolute;
      bottom: 3.125em;
      left: 3.125em;
    }
    &-button{
      border-color: rgba(255, 255, 255, 0.07);
      background-color: rgba(255, 255, 255, 0.03);
      width: 3.125em;
      height: 3.125em;
      @include lastM(0 .9375em 0 0);
      path{
        fill: $white;
      }
      &:hover{
        background-color: rgba(255, 255, 255, 0.08);
      }
    }
    &-pagination{
      position: absolute;
      right: 3.125em;
      bottom: 3.125em;
      z-index: 2;
      width: auto;
      justify-content: flex-end;
      &-bullet{
        background-color: $white;
      }
    }
  }
}

.banner{
  height: 33em;
  position: relative;
  border-radius: .375em;
  padding: 6.75em 3.125em;
  color: $white;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 32.9375em;
  }
  &__desc{
    margin-top: .25em;
  }
  &__nav{
    display: flex;
    margin-top: 2.1875em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0 0 1.875em;
    background-color: transparent;
  }

  .main-slider{
    width: calc(100% + 3.75em);
    max-width: unset;
    margin-left: -1.875em;
    border-radius: 0;
    .swiper{
      &-nav{
        bottom: 1.875em;
        left: 1.875em;
      }
      &-pagination{
        right: 1.875em;
        bottom: 1.875em;
      }
    }
  }

  .banner{
    height: 26.25em;
    border-radius: 0;
    padding: 5.375em 1.875em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      max-width: 20.625em;
    }
    &__desc{
      margin-top: .625em;
      br{
        display: none;
      }
    }
    &__nav{
      margin-top: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .swiper-pagination{
    margin-top: 1.25em;
  }

  .main-slider{
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    .swiper{
      &-nav{
        display: none;
      }
      &-pagination{
        right: 0;
        justify-content: center;
      }
    }
  }

  .banner{
    height: 32.625em;
    padding: 15.625em .9375em 3.75em;
    text-align: center;
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__nav{
      margin-top: .75em;
    }
    &__link{
      width: 100%;
    }
  }
}
