.header-search{
  flex-grow: 1;
  .fg{
    display: flex;
    input{
      flex-grow: 1;
      width: auto;
      font-size: .75em;
      height: 3.75em;
      border: 2px solid $primary;
      background: $white;
      border-radius: 0;
      padding: 0 5em 0 1.25em;
    }
  }
  &__btn{
    padding: 0;
    //position: absolute;
    top: 0;
    right: 0;
    height: 2.8125em;
    border-radius: 0 .3125em .3125em 0;
    width: 2.8125em;
    img{
      width: 1.1875em;
    }
  }
}

.m-search{
  position: relative;
  &__result{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background-color: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    box-shadow: 0 .25em 2.5em 0 rgba(2, 38, 80, 0.13);
    border-radius: 0 0 .3125em .3125em;
    border: 1px solid rgba($primary,.25);
    padding: 1.5em;
    &:before{
      content: '';
      position: absolute;
      bottom: 3.25em;
      left: 0;
      z-index: 2;
      width: 100%;
      height: 4.125em;
      background: linear-gradient(180deg,rgba(255,255,255,.5) 0,#fff 100%);
    }
  }
  &__items{
    @include grid100gap(.625em);
    overflow-y: auto;
    max-height: 23em;
    padding-right: 1.5em;
    &::-webkit-scrollbar {
      width: .375em;
      height: .375em;
    }
    &::-webkit-scrollbar-track {
      background: rgba($primary,.1);
      border-radius: .375em;
    }
    &::-webkit-scrollbar-thumb {
      background: $primary;
      border-radius: .375em;
    }
  }
  &__nav{
    width: calc(100% + 3em);
    margin-left: -1.5em;
    margin-bottom: -1.5em;
  }
  &__link{
    border-radius: 0 0 .3125em .3125em;
    height: 3.25em;
  }
  &._show{
    z-index: 3;
    .m-search__result{
      opacity: 1;
      pointer-events: unset;
    }

  }
}

.search-item{
  display: flex;
  align-items: center;
  &:hover{
    color: $black;
  }
  &__img{
    width: 5.625em;
    height: 5.625em;
    flex-shrink: 0;
    @include center;
    border-radius: .3125em;
    margin-right: 1.25em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__content{
    flex-grow: 1;
    @include grid100gap(.25em);
  }
  &__title{
    font-weight: 600;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1.25em;
    color: #6B7076;
    &:hover{
      color: $primary;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.catalog-btn{
  padding: 0 1.25em;
  height: 2.8125em;
  border-radius: .3125em 0 0 .3125em;
  box-shadow: none;
  &__icon{
    width: 1.5em;
    position: relative;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
  .catalog-btn__icon{
    font-size: 1em;
  }
}


@media screen and (max-width: 992px){
  .header-search{
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    margin-top: .75em;
    &._open{
      opacity: 1;
      pointer-events: unset;
    }
    .fg{
      input{
        border-radius: .42em 0 0 .42em;
      }
    }
  }

  .m-search{
    &__result{
      padding: 0 0 .9375em .9375em;
    }
    &__nav{
      width: calc(100% + .9375em);
      margin-left: -.9375em;
      margin-bottom: -.9375em;
    }
    &__items{
      padding-right: 0;
    }
  }

  .catalog-btn{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .search-item{
    position: relative;
    &__title{
      font-size: .875em;
    }
    &__btn{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
}
