.product-action{
  margin-top: 1.5em;
  &__available{
    display: flex;
    align-items: center;
    color: $green;
    font-weight: 600;
    margin-bottom: 1.25em;
    img{
      margin-right: .8125em;
      width: 1.5625em;
    }
  }
  &__buttons{
    margin-top: 1.875em;
    .compare-btn{
      svg{
        width: 1.5625em;
      }
    }
  }
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #45464a;
    margin-right: 1.875em;
    &:last-child{
      margin-right: 0;
    }
    &-title{
      font-size: 1em;
      line-height: 156%;
    }
    &-icon{
      width: 2.5em;
      height: 2.5em;
      @include center;
      border: 1px solid #cacbcc;
      border-radius: 50%;
      margin-right: .625em;
      flex-shrink: 0;
      svg{
        width: 1.0625em;
      }
    }
  }
}

.product-cart{
  display: flex;
  align-items: center;
  margin-left: 3.125em;
  .cartcalc{
    width: 11.5em;
    height: 3.375em;
  }
  &__prices{
    display: flex;
    align-items: center;
  }
  &__price{
    font-size: 1.625em;
    line-height: 120%;
    font-weight: 600;
  }
  &__price2{
    font-size: 1em;
    text-decoration: line-through;
    color: #9a9da3;
    margin-left: .5625em;
  }
  &__btn{
    margin-left: 1.5625em;
    border: 1px solid $primary;
    &:hover{
      border-color: $orange;
    }
    img{
      width: 1.5625em;
      margin-left: .625em;
    }
    ._view2{
      display: none;
    }
    &._active{
      border-color: $green;
      color: $green;
      background-color: transparent;
      box-shadow: 0 .25em .25em rgba(44, 53, 40, 0.04), 0 .625em 1.0625em rgba(44, 78, 44, 0.19);
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.product-alert{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  background-color: #fff;
  border: 1px solid #DFDFDF;
  box-shadow: 0 1.375em 1.3125em 0 rgba(92, 92, 92, 0.1);
  z-index: 30;
  position: fixed;
  top: 12.0625em;
  right: 2.5em;
  width: 23.75em;
  padding: 1.25em;
  display: grid;
  grid-template-columns: 5em calc(100% - 6.25em);
  gap: 1.25em;
  align-items: center;
  border-radius: .625em;
  @media screen and (min-width: 1501px){
    right: calc((100vw - 1500px) / 2 + 1.25em);
  }
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__img{
    @include imgCover(100%);
    border: 1px solid #dfdfdf;
    border-radius: .3125em;
  }
  &__mess{
    display: flex;
    align-items: center;
    margin-bottom: .25em;
    color: $green;
  }
  &__title{
    @include truncate(1);
    font-size: 1em;
    line-height: 163%;
    font-weight: 600;
  }
}

.product-collection{
  border-top: 1px solid #F3F4F8;
}

.size-modal{
  .modal__dialog{
    width: 85.125em;
  }
}

.size-table{
  margin-top: 2em;
  border: 1px solid #E1E1E1;
  &__top{
    padding: .625em;
    background-color: #F9F9F9;
    @include center;
    min-height: 2.875em;
  }
  &__row{
    display: grid;
    grid-template-columns: 5em calc(100% - 5em);
    border-top: 1px solid #E1E1E1;
    &:nth-child(even){
      background-color: #F9F9F9;
    }
  }
  &__cols{
    display: grid;
    grid-template-columns: repeat(29,calc(100% / 29));
    .size-table__col{
      color: #4B4646;
      border-left: 1px solid #E1E1E1;
    }
  }
  &__col{
    @include center;
    height: 2em;
  }
  @media screen and (min-width: 993px){
    &._type2{
      .size-table__cols{
        grid-template-columns: repeat(25,calc(100% / 25));
      }
    }
  }
}


@media screen and (max-width: 992px){
  .product-action{
    width: calc(100% + 22.875em);
    margin-left: -22.875em;
    margin-top: 1.25em;
    position: relative;
    &__nums{
      justify-content: space-between;
    }
    &__available{
      position: absolute;
      width: 14em;
      justify-content: center;
      bottom: .5em;
      left: 0;
      margin: 0;
    }
    &__buttons{
      padding-left: 16.875em;
    }
  }

  .product-cart{
    margin-left: 2.875em;
    &__prices{
      width: 14em;
      justify-content: center;
    }
    &__btn{
      padding: 0 1.5em;
    }
    .cartcalc{
      margin-left: 0;
    }
  }

  .product-alert{
    top: 6.25em;
    right: 1.875em;
  }

  .size-modal{
    padding-top: 3.75em;
    padding-bottom: 0;
    &:after{
      display: none;
    }
    .modal__dialog{
      border-radius: 0;
      width: 100%;
      max-width: 100%;
      height: 100%;
      overflow-y: auto;
      padding: 1.5625em 1.5625em 0;
    }
  }

  .size-table{
    margin-top: .9375em;
    &__rows{
      display: grid;
      grid-template-columns: repeat(4,25%);
    }
    &__row{
      grid-template-columns: 100%;
      border-left: 1px solid #E1E1E1;
      &:first-child{
        border-left: none;
      }
    }
    &__cols{
      grid-template-columns: 100%;
      .size-table__col{
        border-left: none;
        &:last-child{
          border-bottom: none;
        }
      }
    }
    &__col{
      border-bottom: 1px solid #E1E1E1;
    }
  }
}


@media screen and (max-width: 576px){
  .product-action{
    margin-left: 0;
    width: 100%;
    &__nums{
      @include grid100gap(1em);
    }
    &__available{
      bottom: unset;
      top: .1875em;
      left: unset;
      right: 0;
      margin: 0;
      width: auto;
    }
    &__buttons{
      padding: 0;
    }
  }

  .product-cart{
    margin: 0;
    &__prices{
      width: auto;
      justify-content: unset;
    }
    &__btn{
      margin-left: .9375em;
      flex-grow: 1;
      img{
        display: none;
      }
    }
    .cartcalc{
      flex-shrink: 0;
      width: 7.1875em;
      grid-template-columns: 2.25em calc(100% - 4.5em) 2.25em;
    }
  }

  .product-alert{
    top: 5.625em;
    right: .3125em;
    width: calc(100% - .625em);
  }

  .product-collection{
    border-top: none;
  }

  .size-modal{
    .modal__dialog{
      padding-top: 1.875em;
      padding-left: .9375em;
      padding-right: .9375em;
    }
  }
}
