.header{
  position: relative;
  z-index: 10;
  padding-top: 2.25em;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 5.4375em;
    margin-right: 4.625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &._open-catalog{
    .catalog-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .catalog-btn{
      pointer-events: none;
      ._view2{
        opacity: 1;
      }
      ._view1{
        opacity: 0;
      }
    }
  }
}

.burger{
  display: none;
}

.header-bottom{
  background-color: $black;
  color: $white;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: calc(100% + 5em);
    margin-left: -2.5em;
  }
  &__link{
    min-height: 3.25em;
    display: flex;
    align-items: center;
    font-weight: 600;
    padding: 0 2.5em;
    &:hover{
      background-color: rgba($white,.1);
      color: $white;
      opacity: .9;
    }
    span{
      font-size: .875em;
    }
    img{
      width: 1.25em;
      margin-right: .4375em;
    }
  }
}

.header-middle{
  padding: .9375em 0;
  border-top: 1px solid #F3F4F8;
  &__inner{
    display: flex;
    align-items: center;
    position: relative;
  }
  &__action{
    flex-grow: 1;
    display: flex;
  }
  &__buttons{
    display: none;
  }
}

.header-action{
  display: flex;
  margin-left: 4.625em;
  &__btn{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    color: #6B7076;
    @include lastM(0 1.56em 0 0);
    &:hover{
      color: $primary;
      line,
      path{
        stroke: $primary;
      }
    }
    ._view2{
      display: none;
    }
    svg{
      height: 1.625em;
      width: auto;
      margin-bottom: .125em;
    }
    .pin{
      height: 1.0625em;
      min-width: 1.0625em;
      padding: 0 .125em;
      right: -.1875em;
      top: -.3125em;
      position: absolute;
      span{
        font-size: .625em;
      }
    }
    &-icon{
      position: relative;
    }
    &._hide-desktop{
      @media screen and (min-width: 993px){
        display: none;
      }
    }
  }
}

.mob-menu__bg{
  display: none;
}


@media screen and (max-width: 992px){
  .header-bottom{
    display: none;
  }

  .header{
    padding-top: 0;
    &__logo{
      margin: 0;
      width: 4.0625em;
    }
    &._open{
      .mob-menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
      }
    }
  }

  .header-middle{
    padding: 1.125em 0;
    border-top: none;
    border-bottom: 1px solid #F3F4F8;
    &__mob{
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: space-between;
    }
    &__action{
      flex-grow: 0;
    }
    &__buttons{
      display: flex;
      align-items: center;
    }
    &__btn{
      width: 1.5em;
      @include center;
      @include lastM(0 1.5em 0 0);
      img{
        width: 100%;
        display: block;
      }
      &._favourite{
        width: 1.375em;
      }
      &._cart{
        width: 1.9375em;
      }
      &._active{
        pointer-events: none;
      }
      .pin{
        height: 1.0625em;
        min-width: 1.0625em;
        padding: 0 .125em;
        right: -.25em;
        top: -.375em;
        position: absolute;
        span{
          font-size: .625em;
        }
      }
    }
  }

  .mob-menu{
    position: fixed;
    width: 21.25em;
    height: $vh;
    background: $white;
    top: 0;
    left: 0;
    z-index: 25;
    overflow-y: auto;
    transition: all .6s;
    transform: translateX(-130%);
    &__bg{
      z-index: 24;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: rgba(#000,.7);
    }
  }

  .header-action{
    padding: 1.5em .9375em;
    margin: 0;
    justify-content: flex-end;
    svg{
      margin: 0;
    }
    .text12{
      display: none;
    }
    &__btn{
      @include lastM(0 1em 0 0);
    }
    ._auth{
      margin-right: auto;
      flex-direction: row;
      .header-action__btn-icon{
        margin: 0 .3125em 0 0;
      }
      .text12{
        display: block;
        &._view1{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding-bottom: .83em;
  }
}
