.blog-item{
  padding-bottom: 9.375em;
  &__grid{
    display: grid;
    grid-template-columns: 13.125em calc(100% - 15em);
    gap: 1.875em;
  }
  &__col2{
    max-width: 58.75em;
  }
  &__back{
    text-transform: unset;
    font-weight: 600;
    padding: 0 .9375em;
    span{
      font-size: 1em;
    }
    svg{
      width: 1.5em;
      margin-right: .3125em;
      margin-top: .125em;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
  }
  &__badge{
    @include center;
    padding: 0 1.5em;
    background-color: rgba($primary,.1);
    border-radius: .25em;
    height: 2.5em;
    color: $primary;
    font-weight: 600;
  }
  &__date{
    display: flex;
    align-items: center;
    color: #8f949b;
    font-weight: 600;
    &:before{
      content: '';
      flex-shrink: 0;
      margin: 0 1.0625em;
      background-color: rgba($black,.2);
      width: .375em;
      height: .375em;
      border-radius: 50%;
    }
  }
}

.m-article{
  margin-top: 1.875em;
  p{
    color: #78797C;
    @include lastM(0 0 2em);
  }
  img{
    width: 100%;
    display: block;
    @include lastM(0 0 1.75em);
    border-radius: .3125em;
  }
  &__images{
    margin: 1.75em 0;
    img{
      min-height: 100%;
      object-fit: cover;
    }
  }
}

.blog-collection{
  padding-bottom: 9.375em;
}

.article-ceo{
  margin: 1.875em 0;
  &__item{
    &-top{
      max-width: 19.5em;
      margin: 0 auto;
    }
    &-img{
      @include imgCover(100%);
      border-radius: 50%;
    }
    &-bottom{
      margin-top: 1.875em;
      @include grid100gap(.75em);
      text-align: center;
    }
    &-title{
      font-size: 1.1875em;
      line-height: 116%;
      font-weight: 700;
    }
    &-desc{
      font-size: .75em;
      line-height: 192%;
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){
  .blog-item{
    padding-bottom: 1.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
      position: relative;
    }
    &__back{
      height: 2.25em;
      position: absolute;
      right: 0;
      top: 0;
      span{
        font-size: .8125em;
      }
      svg{
        width: 1em;
        margin-right: .125em;
      }
    }
    &__info{
      margin-top: .25em;
    }
    &__badge{
      padding: 0 .9375em;
      height: 1.8125em;
    }
  }

  .m-article{
    margin-top: .625em;
    img{
      @include lastM(0 0 .625em);
    }
    p{
      @include lastM(0 0 .72em);
    }
    hr{
      height: .5em;
    }
    &__images{
      margin: .625em 0;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .blog-collection{
    padding-bottom: 3.5em;
  }

  .article-ceo{
    margin: .625em 0;
    &__item{
      &-top{
        max-width: 15.315em;
      }
      &-bottom{
        margin-top: .625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog-item{
    &__grid{
      padding-top: 2em;
    }
    &__back{
      right: unset;
      left: -.25em;
      height: auto;
      padding: 0;
      border: none;
      &:hover{
        color: $primary;
        background-color: transparent;
        ._stroke{
          stroke: $primary;
        }
      }
    }
  }

  .blog-collection{
    padding-bottom: 3.125em;
    .h2{
      font-size: 1.125em;
    }
  }

  .article-ceo{
    grid-template-columns: 100%;
  }
}
