.collection2{
  overflow: hidden;
  @media screen and (min-width: 993px){
    .m-section__top{
      align-items: flex-start;
    }
    .m-section__link{
      margin-top: 2.1875em;
    }
  }
}

.product-tabs{
  margin-top: -.9375em;
  display: flex;
  align-items: center;
}

.product-tab{
  flex-shrink: 0;
  @include lastM(0 2.5em 0 0);
  display: flex;
  align-items: center;
  font-weight: 600;
  color: #525864;
  transition: all .3s;
  cursor: pointer;
  span{
    font-size: .875em;
    line-height: 107%;
  }
  svg{
    margin-right: .4375em;
    width: 1.25em;
  }
  &:hover,
  &._active{
    color: $primary;
    path{
      stroke: $primary;
    }
  }
  &._active{
    pointer-events: none;
  }
}

.collection3{
  @media screen and (min-width: 993px){
    .product-card{
      background-color: #fff;
    }
    &__col{
      position: relative;
      z-index: 2;
    }
    &__grid{
      display: grid;
      grid-template-columns: calc(100% - 18em) 16.75em;
      gap: 1.25em;
    }
    .product-slider__slide{
      width: 25%;
      &.swiper-slide-next+.swiper-slide+.swiper-slide+.swiper-slide{
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

.week-slider{
  .product-slider__wrap{
    border: 2px solid $primary;
    border-radius: .3125em;
    overflow: hidden;
    &:hover{
      box-shadow: 0 .25em 1.625em 0 rgba(55, 107, 204, 0.13);
    }
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2.625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 3;
    padding-right: .9375em;
    .swiper-nav{
      position: static;
      height: 100%;
    }
    .swiper-button{
      transform: unset;
      position: static;
      width: .9375em;
      height: auto;
      background-color: transparent;
      box-shadow: none;
      border: none;
      svg{
        width: 100%;
      }
      &:hover{
        path{
          fill: $primary;
        }
      }
    }
  }
  .product-card{
    border-radius: .3125em;
    &:before{
      display: none;
    }
    &__top{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
    &__inner{
      padding: 2.625em .9375em .9375em;
    }
    &__img{
      padding-bottom: 113%;
    }
    &__reviews{
      margin-top: 0;
    }
    &__btn{
      position: absolute;
      right: 0;
      bottom: 0;
      left: unset;
      top: unset;
      width: 2.6875em;
      height: 2.6875em;
      border-radius: .375em 0 0 0;
      background-image: url(../img/cart-icon.svg);
      @include bgImgSize(1.1875em);
      span{
        display: none;
      }
    }
  }
  .product-slider__slide{
    width: 100%;
    &.swiper-slide-active~.swiper-slide{
      opacity: 0;
      pointer-events: none;
    }
  }
  &__title{
    padding: 0 .9375em;
    height: 100%;
    background-color: $primary;
    border-radius: .3125em 0 .3125em 0;
    display: flex;
    align-items: center;
    font-weight: 700;
    color: $white;
    .text13{
      line-height: 115%;
    }
  }
}

.collection4{
  @media screen and (min-width: 993px) {
    .m-section__top {
      width: calc(100% - 6em);
    }
  }
}

.product-slider2{
  @media screen and (min-width: 993px){
    .product-slider__slide{
      position: relative;
      //opacity: 1;
      //pointer-events: unset;
      &:hover{
        z-index: 3;
      }
      &.swiper-slide-next+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide{
        &+.swiper-slide,
        &+.swiper-slide+.swiper-slide,
        &+.swiper-slide+.swiper-slide+.swiper-slide,
        &+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide,
        &+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .product-slider__nav._top{
      position: absolute !important;
      bottom: 100%;
      right: 0;
      margin-bottom: 1.5em !important;
      .swiper-button{
        position: static;
      }
    }
  }

  .product-card{
    margin-bottom: 2.1875em;
  }
}


@media screen and (max-width: 992px){
  .product-tabs{
    margin-top: 1.25em;
    overflow-x: auto;
    @include fullScreenTablet;
    @include nullScroll;
    padding-bottom: 1em;
    margin-bottom: -1em;
  }

  .collection2{
    .m-section__top {
      width: calc(100% - 6em);
    }
    .swiper-nav{
      margin-bottom: 5.125em;
    }
  }

  .collection3{
    &__col2{
      display: none;
    }
  }

  .product-slider2{
    .product-card{
      margin-bottom: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .collection2{
    .m-section__top {
      width: 100%;
    }
    .swiper-nav._top{
      margin-bottom: 4.5em;
    }
  }

  .product-tabs{
    @include fullScreenMob;
  }

  .collection3,
  .collection4{
    .swiper-nav._top{
      margin-bottom: 3.8125em;
    }
  }

  .product-slider2{
    .product-card{
      margin-bottom: 1em;
    }
  }
}
