.page-top{
    padding: 1.875em 0 2.5em;
}

.breadcrumbs {
    margin-bottom: 3.125em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: rgba($black,.5);
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: .125em;
                right: 100%;
                margin-right: .625em;
                font-size: .875em;
                line-height: 130%;
                font-weight: 500;
            }
        }
        &:last-child{
            color: $black;
        }
    }
    a{
        font-size: .875em;
        line-height: 130%;
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding: .625em 0 1.5625em;
    }

    .breadcrumbs{
        margin-bottom: 1.875em;
    }
}


@media screen and (max-width: 576px){
    .page-top{
        padding-top: .3125em;
        &._scroll{
            position: relative;
            padding: 0;
            margin-bottom: 1.25em;
            min-height: 2.75em;
            display: flex;
            align-items: center;
            .container{
                white-space: nowrap;
                overflow-x: auto;
                padding-top: .5em;
                padding-bottom: .5em;
            }
        }
    }

    .breadcrumbs{
        margin-bottom: .9375em;
    }
}
