.m-section{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.last-news{
  &__grid{
    display: grid;
    grid-template-columns: 51.25em calc(100% - 53em);
    gap: 1.25em;
    margin-top: 2.5em;
  }
  &__col{
    .blog-card{
      &__img{
        padding-bottom: 48.4%;
      }
      &__title{
        min-height: unset;
      }
      &__desc{
        font-size: 1em;
      }
    }
  }
}

.news-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        @include grid100gap(1.25em);
        transform: unset !important;
      }
      &-slide{
        padding-right: 0;
      }
      &-pagination{
        display: none;
      }
    }
  }
  @media screen and (min-width: 577px){
    .blog-card{
      display: grid;
      grid-template-columns: 13.75em calc(100% - 15em);
      gap: 1.25em;
      align-items: center;
      &__content{
        margin-top: 0;
      }
    }
    .swiper-nav{
      display: none;
    }
  }
}

.sales2{
  background-color: #F7F8FB;
}

.sales-slider2{
  margin-top: 2.5em;
  position: relative;
  .swiper{
    &-slide{
      width: 25%;
    }
    @media screen and (min-width: 993px){
      &-nav{
        position: static;
      }
      &-button{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin: 0 !important;
        &-prev{
          left: -1.1875em;
        }
        &-next{
          right: -1.1875em;
        }
      }
    }
  }
}

.sale-card2{
  position: relative;
  color: $white;
  border-radius: .375em;
  &__img{
    border-radius: .375em;
    @include imgCover(114.4%);
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 2.1875em 1.5625em;
    display: flex;
    align-items: flex-end;
  }
  &__desc{
    margin-top: .375em;
  }
  &__nav{
    display: flex;
    margin-top: .375em;
  }
  &__badge{
    position: absolute;
    top: 1.5625em;
    left: 1.5625em;
  }
}

.m-badge{
  padding: 0 .5em;
  display: flex;
  align-items: center;
  height: 1.375em;
  background-color: #F45B43;
  color: $white;
  border-radius: .25em;
  &--green{
    background-color: $green;
  }
  &--orange{
    background-color: $orange;
  }
}


@media screen and (max-width: 992px){
  .last-news{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      .blog-card{
        &__img{
          padding-bottom: 56%;
        }
      }
    }
  }

  .news-slider{
    .swiper{
      &-pagination{
        margin-top: 1.25em;
      }
    }
    .blog-card{
      align-items: unset;
      &__top{
        svg{
          display: unset;
        }
      }
      &__title{
        font-size: 1.1875em;
      }
    }
  }

  .sales2{
    overflow: hidden;
  }

  .sales-slider2{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 22.5em;
      }
    }
  }

  .sales2{
    .m-section__top{
      width: calc(100% - 6em);
    }
  }
}


@media screen and (max-width: 576px){
  .m-section{
    &__top{
      position: relative;
      max-width: 75%;
      .h2{
        &:after{
          content: '';
          margin-top: .2em;
          width: .9em;
          height: .9em;
          display: inline-block;
          background-image: url(../img/blue-arrow.svg);
          @include bgImgSize(100%);
        }
      }
    }
    &__link{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }

  .last-news{
    overflow: hidden;
    &__col{
      display: none;
    }
    &__grid{
      margin-top: 1.25em;
    }
  }

  .news-slider {
    position: relative;
    .slider__wrap{
      overflow: unset;
    }
    .swiper {
      &-pagination {
        display: none;
      }
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        width: 18.75em;
        padding-right: .9375em;
      }
      &-nav{
        margin-bottom: 1.875em;
      }
    }
    .blog-card{
      &__content{
        margin-top: 1.25em;
      }
      &__date{
        font-size: .875em;
        line-height: 104%;
      }
    }
  }

  .sales2{
    background-color: transparent;
    .m-section__top{
      width: auto;
    }
  }

  .sales-slider2{
    margin-top: 1.25em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        width: 20.125em;
        padding-right: .75em;
      }
      &-nav{
        margin-bottom: 2em;
      }
    }
  }
}
