.about{
  background-image: url(../img/about-bg.webp);
  background-size: cover;
  //background-position: right top;
  background-position: center;
  &__desc{
    margin-top: .5625em;
    font-weight: 700;
  }
  .m-text{
    margin-top: 1.5625em;
    max-width: 46.5em;
  }
  &__bottom{
    margin-top: 2.8125em;
    display: grid;
    grid-template-columns: calc(100% - 19.75em) 18.5em;
    gap: 1.25em;
  }
  &__nav{
    display: none;
  }
}

.about-slider{
  height: 100%;
  @media screen and (min-width: 577px){
    .swiper{
      &-container{
        width: 100%;
        height: 100%;
      }
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
        gap: 1.25em;
        transform: unset !important;
      }
      &-slide{
        padding-right: 0;
      }
    }
  }
}

.about-card{
  height: 100%;
  background-color: $white;
  border-radius: .3125em;
  padding: 1.25em;
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 2.625em;
    flex-shrink: 0;
    margin-right: .875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.375em);
  }
}

.line-btn{
  font-size: .875em;
  line-height: 164%;
  text-decoration: underline;
  color: $primary;
  font-weight: 600;
}

.our-shop{
  background-color: $primary;
  height: 100%;
  border-radius: .3125em;
  color: $white;
  padding: 1.25em;
  @include grid100gap(.375em);
  box-shadow: 0 .5em 1.125em rgba(0, 0, 0, 0.1), 0 2.0625em 2.0625em rgba(0, 0, 0, 0.09), 0 4.5625em 2.75em rgba(0, 0, 0, 0.05), 0 8.125em 3.25em rgba(0, 0, 0, 0.01), 0 15.25em 3.5625em rgba(0, 0, 0, 0);
}


@media screen and (max-width: 992px){
  .about{
    background-position: unset;
    &__desc{
      margin-top: .375em;
    }
    .m-text{
      margin-top: .75em;
    }
    &__bottom{
      margin-top: 1.5625em;
      grid-template-columns: 100%;
    }
    &__nav{
      display: flex;
      margin-top: .75em;
      ._view2{
        display: none;
      }
    }
    &._show-all{
      .about__nav{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &:not(._show-all){
      .m-text{
        @include truncate(4);
        & *{
          display: inline;
        }
      }
    }
  }

  .about-card{
    @media screen and (min-width: 577px){
      flex-direction: column;
      &__icon{
        margin: 0 0 .875em;
      }
      &__title{
        font-size: 1em;
      }
      &__desc{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    overflow: hidden;
    .h2{
      max-width: 80%;
    }
    &:not(._show-all){
      .m-text{
        -webkit-line-clamp: 7;
      }
    }
  }

  .about-slider{
    .swiper{
      &-container{
        width: calc(100% + .9375em);
      }
      &-slide{
        padding-right: .9375em;
        width: 19.1875em;
      }
    }
  }
}
