.catalog-menu{
  @media screen and (min-width: 993px){
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border-radius: .3125em;
    border: 1px solid rgba($primary,.25);
    min-height: 24.125em;
    background-color: $white;
    box-shadow: 0 .5em 1.125em rgba(46, 48, 54, 0.05), 0 2.0625em 2.0625em rgba(46, 48, 54, 0.04), 0 4.6875em 2.8125em 0 rgba(46, 48, 54, 0.03), 0 8.375em 3.375em 0 rgba(46, 48, 54, 0.01), 0 13em 3.75em rgba(46, 48, 54, 0);
    display: grid;
    grid-template-columns: 15.1875em;
    &__items{
      padding: 1.25em 0;
      min-height: 100%;
      border-right: 1px solid rgba($primary,.25);
    }
  }
}

.m-item{
  &__show{
    display: flex;
    align-items: center;
    padding: 0 2em;
    height: 4em;
    font-weight: 800;
    opacity: .6;
    color: #2E3036;
    transition: all .3s;
    &:hover{
      cursor: pointer;
    }
    svg{
      width: 1.875em;
      margin-right: 1.25em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__groups{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4em / 3));
    gap: 2em;
    height: 100%;
  }
  @media screen and (min-width: 993px){
    &:hover{
      .m-item__show{
        background-color: rgba($primary,.1);
        color: $primary;
        opacity: 1;
        path{
          stroke: $primary;
        }
      }
      .m-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      position: absolute;
      width: calc(100% - 15.1875em);
      height: 100%;
      left: 15.1875em;
      top: 0;
      margin-left: -1px;
      padding: 1.875em 4em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      display: block !important;
    }
  }
}

.m-submenu{
  display: flex;
  flex-direction: column;
  &__show{
    font-weight: 600;
    color: rgba($black,.5);
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__hidden{
    margin-top: 1.25em;
    flex-direction: column;
    flex-grow: 1;
    @media screen and (min-width: 993px){
      display: flex !important;
    }
  }
  &__links{
    column-count: 2;
    column-gap: 1em;
  }
  &__link{
    font-weight: 600;
    display: block;
    width: 100%;
    font-size: .875em;
    line-height: 179%;
    @include lastM(0 0 1.14em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
  &__more{
    font-size: .875em;
    line-height: 179%;
    color: $primary;
    text-decoration: underline;
    font-weight: 600;
  }
}


@media screen and (max-width: 992px){
  .catalog-menu{
    border-top: 1px solid #eaeaea;
  }

  .m-item,
  .m-submenu{
    &__show{
      &:after{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/arrow5.svg);
        @include bgImgSize(100%);
        transition: all .3s;
        margin-left: auto;
        width: .5em;
        height: .5em;
      }
    }
  }

  .m-item{
    padding: 1.25em .9375em;
    border-bottom: 1px solid #eaeaea;
    &._active{
      .m-item__show:after{
        transform: rotate(-180deg);
      }
    }
    &__show{
      padding: 0;
      height: unset;
      opacity: 1;
      color: $black;
      svg{
        opacity: .6;
      }
    }
    &__hidden{
      padding:  1.5em 0 0 .9375em;
      display: none;
    }
    &__groups{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .m-submenu{
    &._active{
      .m-submenu__show:after{
        transform: rotate(-180deg);
      }
    }
    &__show{
      text-transform: uppercase;
      display: flex;
      align-items: center;
      span{
        font-size: .875em;
      }
    }
    &__hidden{
      margin-top: 0;
      padding: 1em 0 0 .9375em;
      display: none;
    }
    &__links{
      column-count: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      font-size: .8125em;
      @include lastM(0 0 .5em);
    }
    &__bottom{
      display: none;
    }
  }
}


