.conf{
  padding: 0 0 5em;
  &__text{
    margin-top: 1.875em;
    font-weight: 400;
    a{
      font-weight: 700;
      text-decoration: underline;
    }
    p,li{
      font-size: .9375em;
      line-height: 180%;
    }
    p{
      @include lastM(0 0 .5em);
    }
  }
  &__hint{
    padding: 2.1875em;
    background: #f4f5f5;
    margin-top: 1.25em;
    .text{
      margin: 0;
      p{
        font-size: .9375em;
        line-height: 180%;
      }
    }
  }
}

@media screen and (max-width: 992px){
  .conf{
    padding-bottom: 2.5em;
    &__hint{
      padding: .75em .9375em;
    }
    &__text{
      margin-top: 1.875em;
      p,li{
        font-size: .875em;
        line-height: 164%;
      }
    }
  }
}

@media screen and (max-width: 576px){
  .conf{
    .h2{
      font-size: 1.0625em;
    }
    &__hint{
      margin-top: 1.25em;
    }
  }
}
