.compare{
  padding-bottom: 9.375em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__inner{
    margin-top: 2.5em;
    position: relative;
  }
  .delete-btn{
    margin-top: .875em;
    @media screen and (min-width: 993px){
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.25em;
    }
  }
  &__grid{
    padding-left: 22.5em;
    position: relative;
  }
  .product-card{
    max-width: 16.75em;
    &:before{
      display: none;
    }
    &__action,
    &__btn{
      opacity: 1;
    }
  }
}

.compare-filter{
  display: flex;
  justify-content: space-between;
  &__select{
    max-width: 100%;
    width: 21.625em;
    flex-shrink: 0;
    label{
      font-weight: 700;
      margin-bottom: .4em;
      line-height: 150%;
    }
  }
  &__radios{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 1.875em;
    .m-radio{
      @include lastM(0 0 .625em);
      input:checked~label{
        font-weight: 700;
      }
      span{
        font-size: .875em;
      }
    }
  }
  @media screen and (min-width: 993px){
    position: absolute;
    width: 18.75em;
    display: block;
    left: 0;
    top: 0;
    z-index: 5;
    &__select{
      label{
        margin-bottom: 1.25em;
        font-size: 1em;
      }
    }
    &__radios{
      padding-top: 1.25em;
    }
  }
}

.delete-btn{
  display: flex;
  align-items: center;
  &:hover{
    color: $red;
    path{
      stroke: $red;
    }
  }
  &__icon{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    @include center;
    border: 1px solid #CACBCC;
    flex-shrink: 0;
    margin-right: .625em;
    svg{
      width: 1.5em;
    }
  }
}

.compare-slider{
  .product-slider__wrap{
    overflow: hidden;
  }
  .product-slider__container{
    width: calc(100% + 3em);
  }
  .product-slider__slide{
    width: calc(100% / 3);
    padding-right: 0;
  }
  .swiper-scrollbar{
    position: absolute;
    top: 32.75em;
    left: 0;
    width: calc(100% - 3em);
    height: .3125em;
    background-color: rgba($primary,.1);
    &-drag{
      background-color: $primary;
    }
  }
  .product-slider2__nav .swiper-button-next, .product-slider__nav .swiper-button-next{
    right: 1.875em;
  }
}

.compare-card{
  &__top{
    height: 32.75em;
    margin-bottom: 3.75em;
  }
}

.compare-row{
  border-top: 1px solid #CACBCC;
  height: 3.1875em;
  display: flex;
  align-items: center;
  &._no-border{
    border-top: none;
  }
  ._gray{
    color: #888888;
  }
}

.compare-sidebar{
  position: absolute;
  background-color: #fff;
  width: 22.5em;
  height: 100%;
  top: 0;
  left: 0;
  padding-top: 36.5em;
}


@media screen and (max-width: 992px){
  .compare{
    overflow: hidden;
    padding-bottom: 3.125em;
    &__inner{
      margin-top: .625em;
    }
    &__grid{
      margin-top: .875em;
      padding-left: 0;
    }
  }

  .delete-btn{
    &__icon{
      width: 1.875em;
      height: 1.875em;
      svg{
        width: 1.125em;
      }
    }
    &__title{
      font-size: .875em;
    }
  }

  .compare-sidebar{
    background-color: transparent;
    padding-top: 27.375em;
    .compare-row{
      border: none;
      &:not(._title){
        align-items: flex-start;
      }
    }
  }

  .compare-row{
    padding: .625em 0;
    &:not(._title){
      display: flex;
      align-items: flex-end;
      height: 3.875em;
      padding-left: .3125em;
      .text16{
        font-size: .875em;
        line-height: 115%;
      }
    }
    &._big{
      height: 4.875em;
    }
  }

  .compare-slider{
    margin-top: 0;
    padding-bottom: 1.25em;
    .product-slider__container{
      width: calc(100% + 1.25em);
    }
    .swiper-scrollbar{
      top: unset;
      bottom: 0;
      width: 100%;
      height: .3125em;
    }
    .product-slider__nav{
      position: static !important;
      .swiper-button{
        position: absolute;
        top: 8.5625em;
        transform: translateY(-50%);
        width: 1.625em;
        height: 1.625em;
        &-prev{
          left: -.8125em;
        }
        &-next{
          right: -.8125em;
        }
      }
    }
  }

  .compare-card{
    &__top{
      padding-right: 1.25em;
      height: 26.5em;
      margin-bottom: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .compare{
    &__inner{
      margin-top: .3125em;
    }
  }

  .compare-filter{
    display: block;
    &__radios{
      padding-top: .875em;
    }
  }

  .compare-slider{
    .product-slider__container{
      width: calc(100% + .9375em);
    }
    .product-slider__slide{
      width: 50%;
    }
    .product-slider__nav{
      .swiper-button{
        top: 6.0625em;
      }
    }
  }

  .compare-card{
    &__top{
      padding-right: .9375em;
      height: 21.5em;
    }
  }

  .compare-sidebar{
    padding-top: 22.375em;
  }
}
