@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/_filter-slider.scss";
.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);
    pointer-events: none;
    border-radius: inherit;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.375em;
    height: 2.375em;
    @include lastM(0 1.25em 0 0);
    @include center;
    &:hover{
      ._fill{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
    }
  }
}

.compare-btn,
.favourite-btn{
  @include center;
}

.favourite-btn{
  &._added{
    path{
      stroke: #CC3737;
      fill: #CC3737;
    }
  }
}

.compare-btn{
  &._added{
    line{
      stroke: $primary;
    }
  }
}
