.delivery-pay{
  padding-bottom: 5em;
  &__subtitle{
    font-size: .9375em;
    line-height: 180%;
    font-weight: 700;
  }
}

.pay,
.delivery{
  &__ways{
    @include grid100gap(1.125em);
    margin-top: 1.5em;
  }
  &__way{
    @include grid100gap(.375em);
  }
}

.pay{
  &__items{
    display: flex;
    margin-top: 1.5em;
  }
  &__item{
    @include center;
    padding: .9375em;
    height: 4.75em;
    border: 1px solid #f4f5f5;
    border-radius: .1875em;
    margin-right: .9375em;
    &:last-child{
      margin-right: 0;
    }
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.delivery{
  margin-top: 1.875em;
  &__way{
    span{
      color: rgba(17, 20, 23, 0.5);
    }
  }
}


@media screen and (max-width: 992px){
  .delivery-pay{
    padding-bottom: 3.125em;
  }

  .pay{
    &__items{
      margin-top: .625em;
    }
    &__item{
      padding: .625em;
      height: 4.375em;
    }
    &__ways{
      margin-top: 1em;
    }
  }

  .delivery{
    margin-top: .9375em;
  }
}


