.product-detail{
  padding: 2.5em 0 5em;
  .tabs{
    justify-content: center;
    margin-bottom: -1.5px;
  }
  @media screen and (min-width: 993px){
    &__top{
      border-bottom: 1px solid #e7e7e7;
    }
  }
  &__bottom{
    margin-top: 1.875em;
  }
}

.product-desc{
  .m-text{
    margin-top: 1.875em;
    p{
      @include lastM(0 0 1.7em);
    }
  }
}

.product-props{
  &__groups{
    margin-top: 1.875em;
  }
}

.prop-group{
  &__top{
    min-height: 3.1875em;
    display: flex;
    align-items: center;
    padding: .25em 0;
  }
  &__title{
    font-size: 1em;
    line-height: 115%;
    font-weight: 600;
  }
  &__item{
    min-height: 3.1875em;
    display: grid;
    grid-template-columns: 37em calc(100% - 37.5em);
    gap: .5em;
    align-items: center;
    padding: .25em 0;
    border-bottom: 1px solid #cacbcc;
    &-title{
      font-size: .875em;
      line-height: 115%;
      color: #888;
    }
    &-desc{
      font-size: 1em;
      line-height: 115%;
    }
  }
}

.reviews{
  &__items{
    margin-top: 1.875em;
    @include grid100gap(.75em);
  }
  .pagination{
    margin-top: 3.75em;
    &__items{
      justify-content: unset;
    }
  }
}

.review-card{
  padding: 1.5em 1.25em;
  display: grid;
  grid-template-columns: 15.625em calc(100% - 15.625em - .75em);
  gap: .75em;
  align-items: flex-start;
  color: #383838;
  border-bottom: 1px solid #F3F4F8;
  &__col{
    @include grid100gap(.5em);
  }
  &__name{
    font-size: 1em;
    line-height: 115%;
  }
  &__date{
    font-size: .875em;
    line-height: 115%;
    color: #6C757D;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-bottom: .5em;
    .rating{
      margin-right: .75em;
    }
    &-count{
      color: #6C757D;
    }
  }
  &__desc{
    font-size: 1em;
    line-height: 140%;
  }
  &__images{
    display: flex;
    margin-top: .125em;
  }
  &__img{
    margin: .625em .625em 0 0;
    width: 8.125em;
    height: 8.125em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: .3125em;
    }
  }
}

.review-form{
  margin-top: 3.125em;
  .rating{
    margin-top: 1.875em;
  }
  .rating-input{
    &__item{
      width: 2em;
      margin-right: .25em;
      path{
        stroke: #BBBBBB;
        fill: none;
      }
    }
  }
  &__grid{
    margin-top: 1.5em;
  }
  .fg{
    textarea{
      height: 13.5em;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 1.875em;
    max-width: 34em;
  }
  &__btn{
    width: 15.5em;
    margin-right: 1.125em;
    flex-shrink: 0;
  }
  &__hint{
    font-size: .75em;
    line-height: 130%;
    color: #97989B;
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
}

.file-input{
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .custom-file-container{
    position: static;
    display: flex;
    flex-direction: column;
  }
  .input-visible,
  .label-container{
    display: none;
  }
  .input-container{
    margin: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    cursor: pointer;
    input{
      cursor: pointer;
      min-width: unset;
      width: 100%;
      height: 100% !important;
    }
  }
  .image-preview{
    overflow: unset;
    background-color: transparent;
    background-image: unset !important;
    padding: .625em 0 0;
    height: auto;
    width: auto;
    max-width: 100%;
    margin: auto 0 0 0;
    display: inline-flex;
    flex-wrap: wrap;
    &:after{
      content: '';
      flex-shrink: 0;
      left: 100%;
      bottom: 0;
      background-image: url(../img/plus.svg);
      @include bgImgSize(1.75em);
      align-self: center;
      margin-top: .625em;
      width: 3.125em;
      height: 3.125em;
      z-index: -1;
      background-color: rgba(#ED9C62,.1);
      border-radius: .3125em;
    }
  }
  .image-preview-item{
    width: 3.125em;
    height: 3.125em;
    border-radius: 0.3125em;
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: center top;
    margin: .625em .625em 0 0;
    box-shadow: none;
    float: unset;
    z-index: 4;
    &-clear{
      padding: 0;
      z-index: 4;
      width: 1.125em;
      height: 1.125em;
      left: unset;
      margin: 0;
      top: -.5em;
      right: -.5em;
      background-color: $orange;
      border-radius: 50%;
      @include center;
      box-shadow: unset;
      &:hover{
        background-color: darken($orange,10%);
      }
      &-icon{
        padding: 0;
        width: 1.125em;
        height: 1.125em;
        background-image: url(../img/close2.svg);
        @include bgImgSize(.75em);
        color: transparent;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          display: none;
        }
      }
    }
  }
  &__wrap{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__inner{
    flex-grow: 1;
    border-radius: .5625em;
    border: 1px dashed rgba($black,.2);
    padding: 1.5625em 1.25em;
    @include center;
    flex-direction: column;
    text-align: center;
    color: #2E3036;
  }
  &__img{
    margin-bottom: 1em;
    width: 1.5625em;
  }
  &__title{
    font-size: 1em;
    line-height: 150%;
    font-weight: 600;
  }
  &__desc{
    margin-top: .3em;
  }
}

.review-hint{
  margin-top: 1.875em;
  padding: 1.5625em 1.25em;
  @include center;
  border: 1px solid rgba($black,.2);
  border-radius: .5625em;
  color: #2C333D;
  img{
    width: 1.5em;
    margin-right: 1.8125em;
  }
}


@media screen and (max-width: 992px){
  .product-detail{
    padding: 1.25em 0 3.75em;
    .tabs{
      border-bottom: 1px solid #e7e7e7;
      margin-bottom: 0;
    }
    .tab{
      padding: 0;
      justify-content: center;
      flex-grow: 1;
      @include lastM(0);
      span{
        font-size: .75em;
      }
    }
  }

  .product-desc{
    .m-text{
      margin-top: .75em;
    }
  }

  .product-props{
    &__groups{
      margin-top: .75em;
    }
  }

  .prop-group{
    &__top{
      min-height: 2.5625em;
    }
    &__item{
      min-height: 2.5625em;
      display: flex;
      justify-content: space-between;
      gap: unset;
      &-title,
      &-desc{
        max-width: 50%;
      }
      &-desc{
        text-align: right;
        font-size: .875em;
      }
    }
  }

  .reviews{
    .pagination__items{
      justify-content: center;
    }
  }

  .review-form{
    .fg{
      textarea{
        height: 14.75em;
      }
    }
  }

  .review-hint{
    align-items: flex-start;
    img{
      margin-right: .6875em;
    }
  }
}


@media screen and (max-width: 576px){
  .product-detail{
    padding-bottom: 1.25em;
  }

  .reviews{
    &__items{
      margin-top: .75em;
      gap: 0;
    }
    .pagination{
      margin-top: .9375em;
      &__items{
        justify-content: center;
      }
    }
  }

  .review-card{
    padding: .8125em 1.25em;
    grid-template-columns: 100%;
    gap: .5em;
    &__img{
      width: 6em;
      height: 6em;
    }
  }

  .review-form{
    .rating{
      margin-top: 1.25em;
    }
    .fg{
      textarea{
        height: 12em;
      }
    }
    &__bottom{
      margin-top: 1.25em;
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 1em;
    }
  }

  .review-hint{
    margin-top: .75em;
  }
}
