.header-top{
  position: absolute;
  bottom: 100%;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 2.25em;
  width: 100%;
  margin-bottom: .9375em;
  transform: translateY(-1px);
  &__address{
    display: flex;
    align-items: center;
    img{
      margin-right: .3125em;
      width: 1.0625em;
    }
  }
  &__links{
    display: flex;
  }
  &__link{
    @include lastM(0 1.875em 0 0);
    font-weight: 600;
    color: #55575B;
    span{
      font-size: .75em;
      line-height: 117%;
    }
  }
}

.header-contacts{
  &__sales{
    margin-left: 1.875em;
  }
  &__title,
  &__link{
    font-size: .8125em;
    line-height: 123%;
    font-weight: 600;
  }
  &__title{
    color: #8F9090;
    margin-right: .77em;
  }
  &__link{
    color: #414349;
  }
}

.header-socials{
  &__item{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #777A82;
    @include lastM(0 1.25em 0 0);
    svg{
      width: 1.25em;
      margin-right: .3125em;
    }
    span{
      font-size: .75em;
    }
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header-top{
    position: static;
    flex-direction: column;
    align-items: unset;
    margin-top: 1.5em;
    margin-bottom: 0;
    padding-bottom: 2.5em;
    height: unset;
    &__links{
      padding: 1.25em .9375em;
      order: -1;
      width: 100%;
      flex-direction: column;
      align-items: flex-start;
      border-bottom: 1px solid #EAEAEA;
    }
    &__link{
      @include lastM(0 0 1.875em);
      span{
        font-size: .875em;
      }
    }
    &__address{
      padding: 0 .9375em;
      margin-top: 1.5em;
    }
  }

  .header-contacts{
    padding: 0 .9375em;
    margin-top: 1.5em;
    flex-wrap: wrap;
    &__sales{
      margin: .75em 0 0;
    }
  }
}


