.collection{
  overflow: hidden;
}

.product-slider2,
.product-slider{
  margin-top: 2.5em;
  position: relative;
  &__wrap{
    overflow: unset;
  }
  &__slide{
    width: 20%;
    transition: all .3s;
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
      &.swiper-slide-active,
      &.swiper-slide-next,
      &.swiper-slide-next+.swiper-slide,
      &.swiper-slide-next+.swiper-slide+.swiper-slide,
      &.swiper-slide-next+.swiper-slide+.swiper-slide+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  @media screen and (min-width: 993px){
    &__nav{
      margin-bottom: 0 !important;
      position: static !important;
      .swiper{
        &-button{
          position: absolute;
          top: 10.3125em;
          transform: translateY(-50%);
          margin-top: 0;
          &-prev{
            left: -.875em;
          }
          &-next{
            right: -.875em;
          }
        }
      }
    }
  }
}

.product-card{
  position: relative;
  &__inner{
    position: relative;
    z-index: 2;
    height: 100%;
  }
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(123.1%);
    background: #f4f4f4;
    border-radius: .3125em;
  }
  &__badges,
  &__badges2{
    position: absolute;
    left: .625em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 3;
    .m-badge{
      @include lastM(0 0 .375em);
    }
  }
  &__badges{
    top: .625em;
  }
  &__badges2{
    bottom: .625em;
  }
  &__reviews{
    display: flex;
    align-items: center;
    margin: .9375em 0 .25em;
    color: #4b4d54;
    .rating{
      margin-right: .3125em;
    }
  }
  &__title{
    font-weight: 600;
    @include truncate(2);
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-top: .3125em;
  }
  &__price{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 700;
  }
  &__price2{
    font-size: .875em;
    line-height: 121%;
    color: #9a9da3;
    text-decoration: line-through;
    margin-left: .64em;
  }
  &__action{
    position: absolute;
    top: .625em;
    right: .625em;
    z-index: 3;
    opacity: 0;
    &-btn{
      background-color: #fff;
      border-radius: 50%;
      box-shadow: 0 0 .125em 0 rgba(0, 0, 0, 0.05), 0 0 .25em 0 rgba(0, 0, 0, 0.04), 0 0 .3125em 0 rgba(0, 0, 0, 0.03), 0 0 .375em 0 rgba(0, 0, 0, 0.01), 0 0 .4375em 0 rgba(0, 0, 0, 0);
      width: 1.5em;
      height: 1.5em;
      margin-bottom: .375em;
      @include center;
      svg{
        width: 1em;
      }
    }
    .compare-btn{
      svg{
        width: .75em;
      }
    }
  }
  .slider{
    &__wrap{
      border-radius: .3125em;
    }
    .swiper{
      &-nav{
        position: absolute;
        right: .75em;
        bottom: 1em;
        opacity: 0;
      }
      &-button{
        background: transparent;
        width: .4375em;
        height: auto;
        @include lastM(0 1em 0 0);
        border: none;
        &:hover{
          path{
            fill: $primary;
          }
        }
      }
    }
  }
  &__btn{
    width: 100%;
    margin-top: .4375em;
  }
  @media screen and (min-width: 993px){
    &:before{
      content: '';
      position: absolute;
      top: -.625em;
      left: -.625em;
      width: calc(100% + 1.25em);
      height: calc(100% + 3.75em);
      background-color: #fff;
      border-radius: .625em;
      box-shadow: 0 .25em .9375em 0 rgba(46, 48, 54, 0.15);
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &:hover{
      &:before{
        opacity: 1;
        pointer-events: unset;
      }
      .product-card__btn,
      .product-card__action,
      .swiper-nav{
        opacity: 1;
      }
    }
    &__btn{
      position: absolute;
      top: 100%;
      left: 0;
      opacity: 0;
    }
  }
}

.rating {
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1em;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: #FFBE18 !important;
        stroke: #FFBE18 !important;
      }
      &~.rating-input__item {
        path {
          fill: #FFBE18 !important;
          stroke: #FFBE18 !important;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: #FFBE18 !important;
    stroke: #FFBE18 !important;
  }
}


@media screen and (max-width: 992px){
  .product-slider{
    margin-top: 2.75em;
    &__slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 576px){
  .product-slider{
    margin-top: 1.5em;
    &__container{
      width: calc(100% + .9375em);
    }
    &__slide{
      width: 50%;
      padding-right: .9375em;
    }
    .swiper-nav._top{
      margin-bottom: 1.25em;
    }
  }

  .product-card{
    .m-badge{
      height: 1.25em;
      span{
        font-size: .6875em;
      }
    }
    &__reviews{
      &-count{
        font-size: .6875em;
      }
    }
    &__price{
      font-size: 1em;
    }
    &__action{
      opacity: 1;
    }
  }
}


