$font1:'RF Dewi',sans-serif;
$font2:'Druk Text Wide Cy TT Bold',sans-serif;
$black: #2e3036;
$white: #fff;
$primary: #376bcc;
$orange: #ed9c62;
$light-grey: #f7f8fb;
$pink: #ff1b6f;
$red: #e52b0e;
$green: #3fa641;


$vh: calc(100 * var(--vh));
$shadow: 0 .25em .25em rgba(40, 44, 53, 0.04),
         0 .625em 1.75em rgba(79, 74, 71, 0.25);
$shadow2: 0 .25em .25em rgba(40, 44, 53, 0.04),
          0 .625em 1.0625em 0 rgba(44, 55, 78, 0.19);
$border: 1px solid $light-grey;
