.footer{
  color: $white;
  &__logo{
    flex-shrink: 0;
    display: block;
    width: 6.875em;
    margin-right: 2.1875em;
    img{
      width: 100%;
      display: block;
    }
  }
  .about-card{
    padding: 0;
    background-color: transparent;
    &__content{
      gap: .5em;
    }
    &__title{
      font-size: 1.1875em;
      line-height: 121%;
      font-weight: 600;
    }
    &__desc{
      line-height: 121%;
      opacity: .52;
    }
  }
  .about-slider{
    @media screen and (min-width: 577px){
      .swiper-wrapper{
        display: flex;
        justify-content: space-between;
      }
      .swiper-slide{
        width: auto !important;
      }
    }
  }
}

.footer-top{
  padding: 1.875em 0;
  background-color: #1A1B1F;
}

.footer-middle{
  padding: 3.125em 0;
  background-color: #17181B;
  border-top: 1px solid rgba($white,.03);
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__col{

  }
}

.footer-info{
  max-width: 21.75em;
  display: flex;
  flex-direction: column;
  height: 100%;
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    font-size: .9375em;
    line-height: 127%;
  }
  &__items{
    margin-top: 1.25em;
  }
  &__item{
    font-size: .8125em;
    line-height: 185%;
    opacity: .51;
  }
}

.footer-socials{
  padding-top: 1.25em;
  margin-top: auto;
  &__wrap{
    display: flex;
    align-items: center;
    margin-top: .5625em;
  }
  &__item{
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    margin-right: 1.1875em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__hint{
    margin-left: -.4375em;
    max-width: 5em;
    color: #7C7D81;
    .text10{
      line-height: 100%;
    }
  }
}

.footer-nav{
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: .625em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 141%;
    font-weight: 600;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .875em);
    display: flex;
    align-items: flex-start;
    color: rgba($white,.58);
    span{
      font-size: .875em;
      line-height: 124%;
    }
  }
}

.footer-contacts{
  max-width: 13em;
  &__title{
    font-size: 1.0625em;
    line-height: 141%;
    font-weight: 600;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .875em;
  }
  &__item{
    @include lastM(0 0 1.125em);
    display: flex;
    color: rgba($white,.58);
    img{
      width: 1.25em;
      margin-right: .75em;
      margin-top: -.09375em;
    }
    &-title{
      font-size: .875em;
      line-height: 121%;
    }
  }
  &__bottom{
    padding-top: 4.5em;
    margin-top: auto;
    display: flex;
    justify-content: flex-end;
  }
  &__by{
    width: 8.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-bottom{
  background-color: #000000;
  padding: .875em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__c{
    font-size: .8125em;
    line-height: 125%;
    br{
      display: none;
    }
  }
  &__conf{
    font-size: .875em;
    line-height: 121%;
    color: rgba($white,.58);
  }
}


@media screen and (max-width: 992px){
  .footer{
    @media screen and (min-width: 577px){
      .about-card{
        &__icon{
          margin-bottom: .75em;
        }
      }
    }
  }

  .footer-middle{
    padding: 1.5em 0;
    &__inner{
      flex-wrap: wrap;
    }
    &__col{
      order: 2;
      width: 24.125em;
      margin-top: 1.5em;
    }
    &__col5{
      width: 18.625em;
      order: 3;
      margin-top: 1.5em;
    }
  }

  .footer-info{
    &__items{
      order: 2;
    }
  }

  .footer-socials{
    display: flex;
    align-items: center;
    &__wrap{
      margin: 0 0 0 .5625em;
    }
  }

  .footer-contacts{
    max-width: 100%;
    &__item{
      @include lastM(0 0 .625em);
    }
    &__bottom{
      padding-top: 1.5em;
      justify-content: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    overflow: hidden;
    .about-slider{
      .swiper{
        &-container{
          width: calc(100% + 1.5em);
        }
        &-slide{
          padding-right: 1.5em;
          width: 17em;
        }
      }
    }
    .about-card{
      align-items: center;
      &__icon{
        margin-right: 1.25em;
        width: 3.3125em;
      }
      &__desc{
        font-size: .75em;
      }
    }
  }

  .footer-middle{
    &__col{
      order: -1;
      margin: 0 0 1.5em;
      width: 100%;
    }
    &__col2,
    &__col3,
    &__col4{
      width: 100%;
    }
    &__col5{
      width: 100%;
    }
  }

  .footer-info{
    &__title{
      font-size: .875em;
    }
  }

  .footer-nav{
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      @include center;
      height: 2em;
      border-bottom: 1px solid rgba(#DAD9D9,.1);
    }
    &__btn{
      @include center;
      width: 1em;
      height: 1em;
      margin-left: .25em;
      svg{
        width: .5625em;
      }
    }
    &__title{
      font-weight: 400;
      font-size: .875em;
      text-transform: uppercase;
    }
    &__hidden{
      margin: 0;
      padding: 1.375em 0 .9375em;
      display: none;
    }
    &__links{
      align-items: center;
    }
    &__link{
      @include lastM(0 0 1.25em);
    }
  }

  .footer-contacts{
    &__bottom{
      justify-content: center;
    }
  }

  .footer-bottom{
    text-align: center;
    &__inner{
      flex-direction: column;
    }
    &__c{
      br{
        display: unset;
      }
    }
    &__conf{
      margin-top: .71em;
    }
  }
}
