.tabs{
  display: flex;
}

.tab{
  height: 2.625em;
  padding: 0 1.5em;
  display: flex;
  align-items: center;
  position: relative;
  color: rgba($black,.8);
  transition: all .3s;
  @include lastM(0 3.25em 0 0);
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: .1875em;
    left: 0;
    bottom: 0;
    background-color: $primary;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: 1em;
    line-height: 120%;
  }
  &:hover{
    color: $primary;
    cursor: pointer;
  }
  &._active{
    color: $primary;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
