.brands{
  border-top: 1px solid #f3f4f8;
  padding: 3.125em 0;
}

.brands-slider{
  display: grid;
  grid-template-columns: 2.375em calc(100% - 24.75em) 8em 2.375em;
  gap: 4em;
  align-items: center;
  .swiper{
    &-button{
      position: static;
      transform: unset;
      margin: 0;
    }
    &-container{
      width: calc(100% + 3.625em);
    }
    &-slide{
      width: auto;
      padding-right: 3.625em;
    }
  }
}

.brand-item{
  height: 3.5em;
  transition: all .3s;
  &:not(:hover){
    opacity: .5;
  }
  img{
    height: 100%;
    width: auto;
    display: block;
  }
}

.categories{
  padding: 3.125em 0;
  background-color: #F7F8FB;
}

.categories-slider{
  position: relative;
  margin-top: 2.5em;
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        display: grid;
        gap: 1.25em;
        grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
        transform: unset !important;
      }
      &-slide{
        grid-column: span 2;
        padding: 0;
      }
      &-nav{
        display: none;
      }
    }
    ._small{
      grid-column: span 1;
      .category-card{
        &__inner{
          padding: 1.5625em;
        }
        &__content{
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }
        &__img{
          ._view1{
            display: none;
          }
          ._view3{
            display: block;
          }
        }
        .h3{
          font-size: .9375em;
          line-height: 153%;
        }
      }
    }
  }
}

.category-card{
  border-radius: .3125em;
  position: relative;
  height: 100%;
  &--white{
    color: $white;
    .category-card__item{
      color: $white;
      background: rgba(255, 255, 255, 0.05);
      &:hover{
        color: $white;
      }
    }
  }
  &--gray{
    .category-card__item{
      background: rgba(#D5E7EF, 0.25);
    }
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: .75em;
  }
  &__content{
    width: 19.75em;
    max-width: 100%;
  }
  &__img{
    @include imgCover(55.7%);
    border-radius: inherit;
    min-height: 100%;
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__top{
    @include grid100gap(.5625em);
  }
  &__date{
    color: #8F949B;
    font-size: .875em;
    line-height: 104%;
  }
  &__items{
    margin-top: .625em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .3125em);
  }
  &__item{
    padding: 0 .6875em;
    height: 1.8125em;
    background: rgba(255, 255, 255, 0.9);
    border-radius: .3125em;
    display: flex;
    align-items: center;
    margin: .3125em .3125em 0 0;
    color: #545c6c;
    font-weight: 600;
    &:hover{
      background: rgba($primary, 0.79);
      color: #fff;
    }
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .brands-slider{
    grid-template-columns: 2.375em calc(100% - 21em) 8em 2.375em;
    gap: 2.75em;
    .swiper{
      &-container{
        width: calc(100% + 2.5em);
      }
      &-slide{
        padding-right: 2.5em;
      }
    }
  }

  .categories-slider{
    margin-top: 1.875em;
    .swiper{
      &-nav{
        margin-bottom: 1.625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .brands{
    padding: 1.875em 0;
    &__link{
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      margin-top: .625em;
    }
  }

  .brands-slider{
    position: relative;
    grid-template-columns: 2.375em calc(100% - 7em) 2.375em;
    gap: 1.125em;
    margin-bottom: 2.875em;
    .swiper{
      &-container{
        width: calc(100% + 1.125em);
      }
      &-slide{
        padding-right: 1.125em;
      }
    }
  }

  .categories{
    overflow: hidden;
  }

  .categories-slider{
    margin-top: 1.25em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 19.125em;
        padding-right: 1em;
      }
    }
  }

  .category-card{
    &__img{
      padding-bottom: 79.3%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__inner{
      padding: 1.5625em;
      align-items: flex-start;
      justify-content: unset;
    }
    &__content{
      max-width: 13.75em;
    }
    .h3{
      font-size: .9375em;
      line-height: 153%;
    }
    &__items{
      display: none;
    }
  }
}
