.auth{
  padding: .625em 0 10em;
  &__inner{
    padding: 2.5em;
    border-radius: .5em;
    border: 1px solid #edf0f2;
    display: grid;
    grid-template-columns: 29em 42.6875em;
    justify-content: space-between;
    align-items: center;
    gap: 1.56em;
  }
  .info-message{
    margin-top: 2.5em;
  }
}

.login-form{
  @include grid100gap(.9375em);
  &__remember{
    display: inline-block;
    margin: 0 auto;
  }
  &__title{
    text-align: center;
    font-weight: 600;
  }
  &__hint{
    text-align: center;
    font-size: 1em;
    line-height: 175%;
    color: #515151;
  }
  &__icon{
    width: 2em;
    margin: 0 auto;
    display: block;
  }
  .m-check{
    label{
      color: #696D70;
    }
    span{
      font-size: .8125em;
    }
  }
  &._type2{
    padding: 2.5em;
    border-radius: 0.1875em;
    border: 1px solid #edf0f2;;
    margin: 0 auto;
    width: 34em;
    max-width: 100%;
  }
}

.login-info{
  height: 100%;
  display: flex;
  padding-left: 7.5em;
  border-left: 1px solid #eaeaea;
  position: relative;
  &__icon{
    position: absolute;
    top: 0;
    left: 3.125em;
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1.81em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    min-height: 2.9375em;
    display: flex;
    align-items: center;
    font-weight: 800;
  }
  .m-text{
    margin-top: 2em;
    max-width: 30.875em;
    color: #515151;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.reg{
  .auth__inner{
    align-items: flex-start;
  }
  .m-text{
    max-width: 21em;
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 0 0 5em;
    &__inner{
      padding: 1.56em;
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
      align-items: flex-start;
    }
    .info-message{
      margin-top: 1.56em;
    }
  }

  .login-form{
    &__link{
      height: 1.75em;
    }
    .m-check{
      display: inline-block;
      margin: 0 auto;
    }
    &__title{
      font-size: 1.3125em;
      margin-bottom: -.67em;
    }
    &__hint{
      font-size: .875em;
      line-height: 200%;
    }
    &._type2{
      padding: 1.25em;
      width: 24.125em;
    }
  }

  .login-info{
    padding: 2.375em 0 0;
    border-left: none;
    position: relative;
    &__icon{
      width: 2.375em;
      top: 1.56em;
      left: 0;
      position: static;
    }
    &__top{
      min-height: unset;
      padding: 0;
      justify-content: center;
    }
    .m-text{
      margin-top: .75em;
      text-align: center;
    }
    &__nav{
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .auth{
    padding-bottom: 4em;
    &__inner{
      grid-template-columns: 100%;
      padding: .9375em;
    }
  }

  .login-info{
    padding-top: 0;
  }
}
