.error404{
  padding-bottom: 9.375em;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__img{
    width: 55em;
    max-width: 100%;
    margin: 0 auto;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__info{
    margin-top: -3.125em;
    @include grid100gap(1.125em);
  }
  &__title{
    font-size: 2.125em;
    line-height: 115%;
    font-weight: 800;
  }
  &__nav{
    margin-top: 1.125em;
    display: flex;
    justify-content: center;
  }
  &__link{
    margin: 0 1em;
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding-bottom: 5em;
    &__img{
      width: 20.625em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__info{
      margin-top: 0;
      gap: .875em;
    }
    &__title{
      font-size: 1.6875em;
    }
    &__nav{
      margin-top: .875em;
    }
    &__link{
      padding: 0 2.875em;
      margin: 0 .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    padding-bottom: 3.125em;
    &__info{
      margin-top: -1.75em;
      br{
        display: none;
      }
    }
    &__nav{
      width: 100%;
      @include grid100gap(.9375em);
    }
    &__link{
      margin: 0;
    }
  }
}
