.about-main{
  &__inner{
    padding: 3.125em 3.125em 4em;
    background-color: rgba($primary,.1);
    position: relative;
    overflow: hidden;
  }
  &__content{
    max-width: 36.5em;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 34em;
    opacity: .8;
  }
  &__list{
    margin-top: 1.8125em;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: 44em;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}

.m-list{
  @include grid100gap(1.25em);
  li{
    font-size: .875em;
    line-height: 171%;
    position: relative;
    padding-left: 1.28em;
    &:before{
      content: '';
      position: absolute;
      top: .57em;
      left: 0;
      width: .57em;
      height: .57em;
      border-radius: 50%;
      background-color: $orange;
    }
  }
}

.mission{
  padding: 5em 0;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      min-height: 100%;
      object-fit: cover;
      border-radius: .3125em;
    }
  }
  &__content{
    padding: 3.125em;
    border-radius: .3125em;
    background-color: $primary;
    color: $white;
  }
  &__desc{
    margin-top: 1.5em;
  }
}

.about-brands{
  padding: 5em 0;
  &__inner{
    display: grid;
    grid-template-columns: 41.875em calc(100% - 41.875em - 2.6875em);
    gap: 2.6875em;
  }
  &__content{
    padding: 3.125em;
    border-radius: .3125em;
    background-color: $primary;
    color: $white;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .5em));
    gap: .75em;
    margin-top: 3.125em;
  }
  &__item{
    border-radius: .3125em;
    border: 1px solid rgba(255, 255, 255, 0.1);
    @include center;
    &:hover{
      border-color: $white;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    img{
      display: block;
      width: 100%;
      border-radius: .3125em;
      min-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    &__inner{
      padding: 1.25em;
    }
    &__img{
      width: 19.4375em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      max-width: 25.6875em;
    }
    .h3{
      line-height: 169%;
      font-size: .8125em;
    }
    &__desc,
    &__list{
      margin-top: .0625em;
    }
  }

  .m-list{
    gap: .375em;
    li{
      font-size: .8125em;
      line-height: 169%;
    }
  }

  .mission{
    padding: 1.5625em 0 1.25em;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__content{
      padding: 1.25em;
      order: -1;
    }
    &__desc{
      margin-top: 0;
      .text14{
        font-size: .8125em;
        line-height: 154%;
      }
    }
  }

  .about-brands{
    padding: 1.25em 0 3.125em;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    &__content{
      padding: .9375em;
    }
    &__items{
      margin-top: .9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    &__inner{
      padding: .625em .625em 19.1875em;
    }
    &__img{
      width: 100%;
      top: unset;
      bottom: 0;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .mission{
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      order: unset;
    }
    &__img{
      min-height: 21.25em;
    }
  }

  .about-brands{
    padding-top: 1.5625em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
  }
}
