.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 1.0625em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: rgba($primary,.1);
    border: none;
    box-shadow: none;
    height: .3125em;
    border-radius: .3125em;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      border: .3125em solid $white;
      background-color: $light-grey;
      box-shadow: 0 0 .1875em 0 rgba(0, 0, 0, 0.1), 0 0 .375em 0 rgba(0, 0, 0, 0.09), 0 0 .5em 0 rgba(0, 0, 0, 0.05), 0 0 .5625em 0 rgba(0, 0, 0, 0.01), 0 0 .625em 0 rgba(0, 0, 0, 0);
      width: 1.0625em;
      height: 1.0625em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 1.25em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.06em;
    &-title{
      position: absolute;
      top: 50%;
      left: 1.25em;
      transform: translateY(-50%);
    }
  }
  .fg{
    input{
      height: 3.21em;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      line-height: 140%;
      padding: 0 2em;
      border-radius: .355em;
    }
    &__title{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: .71em;
      font-size: .875em;
      opacity: .5;
    }
  }
}
