.order{
  padding-bottom: 9.375em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 30em) 28.75em;
    gap: 1.5625em 1.25em;
  }
}

.has-reg{
  padding: .375em 1.375em;
  border: $border;
  border-radius: .25em;
  &__info{
    font-weight: 600;
    img{
      margin-right: .75em;
      width: 1.3125em;
    }
  }
  &__btn{
    height: 2.5em;
    padding: 0 .75em;
    margin-left: 1.875em;
    span{
      font-size: .6875em;
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.user-data{
  margin-top: 1.25em;
  &--border{
    margin-top: 2.5em;
    padding-top: 1.875em;
    border-top: 1px solid rgba($black,.1);
  }
  &__sub-fields,
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.5em 1.25em;
    margin-top: 1.25em;
    textarea{
      height: 10.25em;
    }
  }
  &__sub-fields{
    margin-top: 0;
  }
}

.pay-type,
.delivery-type{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
}

.delivery-field{
  input:checked~.delivery-field__inner{
    border-color: $primary;
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  &__inner{
    padding: 1.5625em;
    border: 1px solid rgba(46, 48, 54, 0.25);
    border-radius: .3125em;
    height: 100%;
    display: flex;
    align-items: flex-start;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/radio.svg);
      @include bgImgSize(0);
      border-radius: 50%;
      border: 1px solid rgba($black,.1);
      margin-right: 1em;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 140%;
  }
  &__desc{
    margin-top: .33em;
    font-size: .875em;
    line-height: 140%;
    color: #929798;
    span{
      color: $black;
    }
  }
  &__nav{
    margin-top: .3125em;
    display: flex;
    position: relative;
    z-index: 3;
  }
  &__btn{
    font-size: .9375em;
    line-height: 140%;
    color: $primary;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
  &__items{
    display: flex;
    position: relative;
    z-index: 3;
    margin-top: .75em;
    .m-radio{
      @include lastM(0 1.875em 0 0);
    }
  }
}

.pay-type{
  display: none;
  &._active{
    display: grid;
  }
}

.cart-orders{
  @include grid100gap(2.5em);
}

.cart-row2{
  grid-template-columns: calc(100% - 9.625em) 9.625em;
  gap: .9375em 0;
  padding: 0;
  @media screen and (min-width: 993px){
    border: none;
  }
  .cart-row__content{
    grid-column: 1/-1;
  }
  .cart-row__prices{
    display: none;
  }
  .cart-row__count{
    order: 2;
  }
  .cart-row__price2{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .order{
    padding-bottom: 5em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__subtitle{
      font-size: 1.1875em;
      line-height: 232%;
    }
  }

  .has-reg{
    padding: .625em .9375em;
  }

  .user-data{
    margin-top: 0;
    &--border{
      margin-top: .9375em;
      padding-top: 0;
      border: none;
    }
    &__fields{
      margin-top: 0;
      gap: 1.25em;
      grid-auto-flow: dense;
      ._last{
        order: 3;
      }
    }
    &__sub-fields{
      gap: 1.25em;
      grid-column: 1/-1;
    }
  }

  .pay-type,
  .delivery-type{
    margin-top: 0;
  }

  .delivery-field{
    &__inner{
      padding: 1.25em .75em 1.25em 1.25em;
    }
  }

  .cart-orders{
    gap: .625em;
  }

  .cart-row2{
    padding: .9375em;
  }
}


@media screen and (max-width: 576px){
  .order{
    padding-bottom: 3.125em;
    &__grid{
      gap: 1.25em;
    }
  }

  .has-reg{
    flex-direction: column;
    padding: .9375em;
    &__btn{
      margin: .875em 0 0;
    }
  }

  .user-data{
    &__fields,
    &__sub-fields{
      grid-template-columns: 100%;
    }
  }

  .pay-type,
  .delivery-type{
    grid-template-columns: 100%;
    gap: .625em;
  }

  .order-sum{
    padding: .625em;
  }
}
