.lk{
  padding-bottom: 5em;
  &__grid{
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: 20.625em calc(100% - 20.625em - 1.875em);
    gap: 1.875em;
  }
}

.menu-btn{
  display: none;
}

.lk-menu{
  position: sticky;
  top: 1em;
  &__bg,
  &__close{
    display: none;
  }
}

.lk-nav{
  overflow: hidden;
  background: #FFF;
  border: 1px solid #edf0f2;
  border-radius: .3125em;
}

.lk-link{
  min-height: 4.625em;
  display: flex;
  align-items: center;
  padding: .75em 1.875em;
  border-bottom: 1px solid #edf0f2;
  &:last-child{
    border-bottom: none;
  }
  &._active,
  &:hover{
    border-color: $black;
    background-color: $black;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .625em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 600;
  }
}

.lk-buttons{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  gap: 1.25em;
}

.lk-btn{
  min-height: 11.25em;
  position: relative;
  padding: 2.5em;
  border: 1px solid rgba(46, 48, 54, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: .3125em;
  .lk-link__count{
    position: absolute;
    top: -.625em;
    right: -.625em;
  }
  &__icon{
    height: 3.75em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    svg{
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 140%;
  }
  &:hover,
  &._active{
    background-color: rgba($primary,.05);
    border-color: $primary;
    color: $black;
    path{
      stroke: $primary;
    }
  }
}

._hide-menu-on-desktop{
  @media screen and (min-width: 993px){
    .lk-buttons{
      display: none;
    }
  }
}

.profile-form,
.address-form{
  width: 61.25em;
  max-width: 100%;
  display: grid;
  gap: 1.5em;
  margin-top: 1.875em;
}

.profile-form{
  grid-template-columns: repeat(2,calc(50% - .75em));
  &__type{
    grid-column: 1/-1;
    display: flex;
    align-items: center;
    &-items{
      display: flex;
      align-items: center;
    }
    .m-radio{
      margin-left: 1.125em;
    }
  }
  &:not(._co){
    ._hide-on-personal{
      display: none;
    }
  }
  &__nav{
    margin-top: 1.625em;
  }
  &__btn{
    min-width: 16.25em;
  }
}

.address-form{
  grid-template-columns: repeat(6,calc(100% / 6 - 7.5em / 6));
  .fg{
    @media screen and (min-width: 993px){
      &--two{
        grid-column: span 2;
      }
      &--three{
        grid-column: span 3;
      }
      &._smaller{
        width: calc(100% - 1em);
        input{
          padding-right: .625em;
        }
      }
      &._bigger{
        margin-left: -1em;
        width: calc(100% + 1em);
        input{
          padding-right: .625em;
        }
      }
    }
  }
  &__nav{
    margin-top: 1em;
    display: flex;
    grid-column: span 3;
    .m-btn-blue{
      width: 16.25em;
      flex-shrink: 0;
    }
    .m-btn-blue-outline{
      flex-grow: 1;
      margin-left: .875em;
    }
  }
}

.pass-form{
  width: 29.75em;
  max-width: 100%;
  @include grid100gap(1.875em);
  margin-top: 1.875em;
  &__nav{
    margin-top: .625em;
    display: flex;
  }
  &__btn{
    width: 16.25em;
    max-width: 100%;
  }
}


@media screen and (max-width: 992px){
  .lk{
    &__grid{
      margin-top: 1.5625em;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col{
      position: relative;
      z-index: 3;
    }
    &__name{
      display: none;
    }
  }

  .lk-menu{
    @include menu;
    &._open{
      transform: translateX(0);
      &~.lk-menu__bg,
      &~.lk-menu__close{
        display: block;
      }
    }
  }

  .lk-nav{
    border-radius: 0;
    border: none;
  }

  .lk-buttons{
    grid-template-columns: repeat(6,calc(100% / 6 - 3.125em / 5));
    gap: .625em;
    margin-bottom: 1.5em;
  }

  .lk-btn{
    align-items: center;
    text-align: center;
    justify-content: unset;
    padding: .9375em .625em;
    min-height: 6.25em;
    &__icon{
      height: 2.3125em;
      margin-bottom: .3125em;
      justify-content: center;
    }
    &__title{
      font-size: .8125em;
      line-height: 108%;
      min-height: 2.1em;
      @include center;
    }
  }

  .profile-form{
    margin-top: .9375em;
    &__nav{
      margin-top: -.625em;
    }
    &__btn{
      min-width: unset;
      width: 100%;
    }
  }

  ._full-tablet{
    grid-column: 1/-1;
  }

  ._hide-menu-on-tablet{
    .lk-buttons{
      display: none;
    }
  }

  ._hide-menu-btn-on-tablet{
    @media screen and (min-width: 577px){
      .menu-btn{
        display: none;
      }
    }
  }

  ._hide-menu-btn{
    .menu-btn{
      display: none;
    }
  }

  .menu-btn{
    @include center;
    border-radius: .3125em;
    height: 2.5625em;
    background: #f2f6fe;
    width: 100%;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 1.5em;
    span{
      font-size: .8125em;
    }
    svg{
      width: 1.4375em;
      margin-right: .625em;
    }
  }

  .address-form{
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: .625em 1.25em;
    margin-top: .625em;
    .fg{
      &--full-tablet{
        grid-column: 1/-1;
      }
    }
    &__nav{
      margin-top: .625em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
      .m-btn{
        width: 100%;
        margin: 0;
      }
    }
  }

  .pass-form{
    margin-top: .9375em;
    gap: 1.25em;
    width: 100%;
    &__nav{
      margin-top: 0;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .lk{
    padding-bottom: 3.125em;
    &__grid{
      margin-top: 1.25em;
    }
  }

  .lk-buttons{
    grid-template-columns: repeat(2,calc(50% - .3125em));
  }

  .lk-btn{
    padding: .875em .625em;
    min-height: 5.6875em;
    &__title{
      min-height: unset;
      font-size: .9375em;
      line-height: 140%;
    }
  }

  .profile-form{
    grid-template-columns: 100%;
  }

  .menu-btn{
    margin-bottom: 1.25em;
  }

  ._hide-menu-on-mob{
    .lk-buttons{
      display: none;
    }
  }

  .address-form{
    &__nav{
      grid-template-columns: 100%;
    }
  }
}
