.cart{
  padding-bottom: 6.875em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 30em) 28.125em;
    gap: 1.5625em 1.875em;
  }
}

.cart-table{
  border: 1px solid #ececec;
  border-radius: .625em;
  &__head{
    height: 2.875em;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 31.75em) 8em 13.125em 10.625em;
    padding-left: 1.25em;
    align-items: center;
  }
}

.cart-row{
  padding: 1.25em 0 1.25em 1.25em;
  border-top: 1px solid #ececec;
  position: relative;
  &__content{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 5.625em;
    height: 5.625em;
    border-radius: .3125em;
    @include imgCover(100%);
    padding-bottom: 0;
    margin-right: .9375em;
    flex-shrink: 0;
  }
  &__title{
    font-weight: 600;
    font-size: 1em;
    line-height: 162%;
  }
  &__price{
    font-size: 1.25em;
    line-height: 110%;
    color: $primary;
  }
  &__price2{
    font-size: .9375em;
    line-height: 147%;
    text-decoration: line-through;
    color: #8f9296;
  }
  &__sum{
    .cart-row__price2{
      display: none;
    }
  }
  &__action{
    position: absolute;
    right: 2.5em;
    top: 50%;
    transform: translateY(-50%);
  }
  &__remove{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
  &__props{
    display: flex;
    align-items: center;
    margin-top: .3125em;
  }
  &__prop{
    display: flex;
    align-items: center;
    margin-right: 1.875em;
    &:last-child{
      margin-right: 0;
    }
    &-title{
      font-weight: 600;
      color: #8f9296;
    }
    .color-view{
      margin-left: .625em;
    }
  }
  &__size{
    &-count{
      margin-left: .625em;
    }
  }
}

.color-view{
  border: 1px solid rgba(46, 48, 54, 0.25);
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  @include center;
  span{
    width: .875em;
    height: .875em;
    display: block;
    border-radius: 50%;
  }
}

.cart-sum{
  position: sticky;
  top: 1em;
  padding: 1.5625em;
  border-radius: 0.625em;
  border: 1px solid #ececec;
  @include grid100gap(1.5625em);
  &__title{
    font-size: 1.75em;
    line-height: 157%;
    font-weight: 600;
  }
  &__props{
    @include grid100gap(1.5625em);
  }
  &__btn{
    width: 100%;
    height: 3.125em;
    padding: 0;
  }
  &__hint{
    border-radius: 0.1875em;
    border: 1px dashed #7EACB8;
    background: #F6FBFC;
    padding: .625em;
    //color: $grayText;
  }
  &__nav{
    @include grid100gap(.625em);
  }
  &__info{
    &-item{
      padding: .625em 0;
      @include grid100gap(.19em);
      border-bottom: 1px solid $black;
    }
    &-title{
      font-size: 1em;
      line-height: 169%;
      font-weight: 500;
    }
    &-desc{
      font-size: 1em;
      line-height: 140%;
    }
  }
  &__hint2{
    text-align: center;
    color: #4e5154;
    a{
      text-decoration: underline;
    }
  }
  &__sale{
    border: 1px solid #ececec;
    border-radius: .3125em;
    padding: .5em 1.25em;
    min-height: 3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-title{
      font-weight: 600;
      font-size: .75em;
      span{
        font-weight: 400;
      }
    }
    &-num{
      font-size: .875em;
      color: $red;
      font-weight: 600;
    }
  }
}

.prop-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .375em;
    border-top: 1px dashed #D1D1D1;
    margin: 0 .94em;
  }
  &__title{
    &:not(.p-text){
      color: #2C333D;
    }
    &._big{
      font-size: 1.375em;
      line-height: 100%;
    }
  }
  &__desc{
    order: 2;
    &._big{
      font-size: 1.25em;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 187%;
  }
}

.promo-code{
  @include grid100gap(.625em);
  &._filled{
    .promo-code__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    text-transform: unset;
    font-weight: 600;
    span{
      font-size: .9375em;
    }
  }
  &__clear{
    position: absolute;
    width: 1em;
    height: 1em;
    top: 1.375em;
    right: 1em;
    @include center;
    opacity: 0;
    pointer-events: none;
    &:hover{
      path{
        stroke: $red;
      }
    }
    svg{
      width: .6875em;
    }
  }
  &__message{
    border-radius: 0.25em;
    background: #ecf7ec;
    height: 3.375em;
    @include center;
    color: $green;
    font-weight: 600;
  }
  &._success{
    .fg{
      input{
        border-color: #1b9665;
        background-image: url(../img/check2.svg);
        background-size: 1em auto;
        background-repeat: no-repeat;
        background-position: right 3em center;
      }
    }
  }
  &._error,
  &._error2{
    .fg{
      input{
        border-color: $red;
        color: $red;
      }
    }
  }
  &._error{
    .promo-code__message{
      color: $red;
      background: rgba($red,.07);
      img{
        width: 1.5em;
      }
    }
  }
  &._error2{
    .promo-code__message{
      //color: $p;
      border: 2px solid $black;
      background: transparent;
    }
  }
}

.cart-empty{
  padding: 2.5em 0 9.375em;
  &__inner{
    @include grid100gap(1.25em);
    text-align: center;
    width: 44.1875em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__img{
    width: 10.6875em;
    margin: 0 auto;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
    .pin{
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  &__title{
    font-size: 2.125em;
    line-height: 115%;
    font-weight: 800;
  }
  &__desc{
    opacity: .8;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding-bottom: 1.25em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cart-table{
    border: none;
    &__head{
      display: none;
    }
    &__items{
      @include grid100gap(.625em);
    }
  }

  .cart-row{
    border: 1px solid #ececec;
    border-radius: .3125em;
    grid-template-columns: calc(100% - 21.125em) 11.5em 8.125em 1.5em;
    padding: .9375em;
    &__content{
      align-items: unset;
    }
    &__img{
      width: 3.8125em;
      height: 3.8125em;
      margin-right: .625em;
    }
    &__title{
      font-size: .875em;
      line-height: 143%;
    }
    &__prices{
      display: none;
    }
    &__sum{
      .cart-row__price2{
        display: block;
      }
    }
    &__action{
      position: static;
      transform: unset;
    }
    &__size{
      &-count{
        font-size: .875em;
        margin-left: .71em;
      }
    }
  }

  .cart-sum{
    gap: .9375em;
    padding: .9375em;
    &__title{
      font-size: 1.375em;
      line-height: 132%;
    }
    &__props{
      gap: .9375em;
    }
  }

  .cart-empty{
    padding: 1.5em 0 3.125em;
    &__img{
      width: 6.75em;
    }
    &__title{
      font-size: 1.6875em;
      line-height: 144%;
    }
  }
}


@media screen and (max-width: 576px){
  .cart-table{
    &__items{
      gap: 1.25em;
    }
  }

  .cart-row{
    grid-template-columns: calc(100% - 10em) 9.5em;
    gap: 1.125em .5em;
    &__content{
      padding-right: 1.875em;
      grid-column: 1/-1;
    }
    &__action{
      position: absolute;
      right: .9375em;
      top: .9375em;
    }
    &__count{
      order: 2;
    }
  }

  .cart-empty{
    &__title{
      font-size: 1.25em;
    }
  }
}
