.m-title{
  display: flex;
  &__num{
    margin: -.625em 0 0 .625em;
    opacity: .7;
    white-space: nowrap;
  }
}

.catalog{
  padding-bottom: 9.375em;
  &__grid{
    display: grid;
    grid-template-columns: 16.125em calc(100% - 18em);
    gap: 1.875em;
  }
  .empty-message{
    margin-top: 3.75em;
  }
}

.filter{
  &__close,
  &__bg,
  &__btn{
    display: none;
  }
}

.filter-btn{
  display: none;
}

.filter-group{
  padding-bottom: 1.5625em;
  margin-bottom: 1.5625em;
  border-bottom: 1px solid rgba(#aaa,.2);
  &:last-child{
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: none;
  }
  &._active{
    .filter-group__btn{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    font-size: 1em;
    line-height: 144%;
    font-weight: 800;
  }
  &__btn{
    width: .75em;
    @include center;
    opacity: .4;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 1.25em;
    display: none;
    position: relative;
    &._shadow{
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 2;
        width: calc(100% - .5em);
        height: 1.5em;
        background: linear-gradient(180deg, rgba(#fff,.5) 0%, #fff 100%);
      }
    }
  }
  &__checks{
    padding-right: .5em;
    padding-bottom: 1px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-height: 14.75em;
    overflow-y: auto;
    @include scroll;
    .m-radio,
    .m-check{
      @include lastM(0 0 .625em);
      input:checked~label{
        font-weight: 700;
      }
      &__count{
        font-weight: 400;
        opacity: 0.5;
      }
      span{
        font-size: .875em;
        margin-right: .25em;
      }
    }
    .m-color{
      @include lastM(0 0 .625em);
    }
  }
  &__sizes{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .5625em);
    .m-size{
      width: 3.5625em;
      margin: .5625em .5625em 0 0;
    }
  }
}

.m-color{
  display: flex;
  align-items: center;
  padding-left: 2.5em;
  min-height: 1.5em;
  &__view{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    span{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 50%;
    }
  }
  label{
    span{
      font-size: .875em;
      line-height: 170%;
    }
  }
  &__count{
    opacity: .5;
  }
  input:checked{
    &~.m-color__view{
      border: 1px solid #acacac;
      padding: .1875em;
    }
    &~label{
      .m-color__title{
        font-weight: 700;
      }
    }
  }
  input:disabled{
    pointer-events: none;
    &~.m-color__view{
      border: 1px solid #acacac;
      padding: .1875em;
      &:before{
        content: '';
        position: absolute;
        height: 1px;
        top: 50%;
        transform: translateY(-50%) rotate(-45deg);
        left: 0;
        width: 100%;
        background-color: #acacac;
      }
    }
  }
}

.m-size{
  input:checked~label{
    background-color: $primary;
    border-color: $primary;
    color: $white;
  }
  input:hover~label{
    border-color: $primary;
  }
  input:disabled{
    pointer-events: none;
    &~label{
      background: #f4f4f4;
      border-color: #f4f4f4;
      color: rgba($black,.4);
    }
  }
  label{
    min-width: 100%;
    height: 2.1875em;
    @include center;
    border: 1px solid rgba(46, 48, 54, 0.25);
    border-radius: .3125em;
  }
  span{
    font-size: .875em;
  }
}


@media screen and (max-width: 992px){
  .m-title{
    &__num{
      margin: 0 0 0 .3125em;
    }
  }

  .catalog{
    padding-bottom: 5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col{
      position: relative;
      z-index: 5;
    }
    .empty-message{
      margin-top: 2.5em;
    }
  }

  ._open-filter{
    .header{
      z-index: 1;
    }
    .filter{
      transform: translateX(0);
    }
    .filter__close{
      display: flex;
    }
    .filter__bg{
      display: block;
    }
  }

  .filter{
    @include menu;
    padding: 3.125em;
    width: 31.75em;
    &._open-category{
      overflow: hidden;
      .category-menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__close{
      width: 1.875em;
      height: 1.875em;
      background-image: url(../img/filter-close.svg);
      @include bgImgSize(100%);
      left: 26.75em;
      top: 3.125em;
    }
    &__bg{
      opacity: 0.8;
    }
    &__btn{
      width: 100%;
      display: flex;
      margin-top: 1.5625em;
    }
  }

  .filter-group{
    &._small{
      max-width: 16.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .m-title{
    .h2{
      max-width: 10em;
    }
  }

  .catalog{
    padding-bottom: 2.5em;
  }

  .filter{
    width: 18em;
    padding: 1.875em .9375em;
    &__close{
      left: 18.375em;
      width: 1.25em;
      height: 1.25em;
      top: .625em;
      background-image: url(../img/filter-close2.svg);
      path{
        stroke: $white;
      }
    }
  }
}
