.order-thank{
  width: 58.75em;
  max-width: 100%;
  margin: 2.5em auto 0;
  &__top{
    padding: .75em .9375em;
    text-align: center;
    border-radius: .3125em;
    background: rgba($green,.1);
    color: $green;
  }
  &__title{
    margin: 2.5em 0 1.25em;
    padding-top: 1.875em;
    border-top: 1px solid rgba($black,.1);
  }
}

.order-info{
  margin-top: 1.25em;
  border-radius: 0.3125em;
  border: 1px solid rgba(46, 48, 54, 0.25);
  padding: 2.1875em 4em;
  display: flex;
  justify-content: space-between;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      @media screen and (min-width: 993px){
        flex-grow: 1;
        &:after{
          content: '';
          height: 4.5em;
          width: 1px;
          border-right: 1px solid #cacbcc;
          min-height: 100%;
          flex-shrink: 0;
          margin: 0 auto;
        }
      }
    }
    &-inner{
      @include grid100gap(.625em);
    }
  }
  &__title{
    font-size: .75em;
    line-height: 140%;
    color: #929798;
  }
  &__desc{
    font-size: 1em;
    line-height: 140%;
    font-weight: 600;
  }
}

.order-detail{
  border: 1px solid #edf0f2;
  border-radius: .5em;
  &__head{
    padding: 1.25em;
    min-height: 4em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-title{
      font-size: .875em;
      line-height: 171%;
      text-transform: uppercase;
      font-weight: 700;
    }
  }
}

.detail-item{
  padding: 1em 1.25em;
  min-height: 4em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #edf0f2;
  &__title,
  &__title2,
  &__desc{
    font-size: .875em;
    line-height: 150%;
  }
  &__desc{
    font-weight: 700;
  }
  &__title2{
    text-transform: uppercase;
    font-weight: 700;
  }
  &__desc2{
    font-size: 1em;
    line-height: 150%;
    text-transform: uppercase;
    font-weight: 700;
    &._muted{
      opacity: .5;
    }
  }
  &__sum{
    font-size: 1.5em;
    font-weight: 700;
    text-transform: uppercase;
  }
}

.order-delivery{
  border: 1px solid #f1f1f1;
  margin-top: 2.5em;
  border-radius: .5em;
  position: relative;
  &__nav{
    position: absolute;
    border: 1px solid #f1f1f1;
    border-radius: .5em 0;
    right: -1px;
    bottom: -1px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 1.125em 1.875em;
    display: flex;
    align-items: center;
    font-weight: 600;
    text-transform: uppercase;
    border-right: 1px solid #f1f1f1;
    &:last-child{
      border-right: none;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    span{
      font-size: .6875em;
    }
    svg{
      width: 1.125em;
      margin-right: .5em;
    }
  }
  &__top{
    min-height: 4em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0 0 1.875em;
  }
  &__bottom{
    padding: .8125em 1.875em 1.875em;
    display: flex;
  }
  &__name{
    font-size: 1.1875em;
    line-height: 121%;
    font-weight: 600;
  }
  &__type{
    font-weight: 600;
    display: flex;
    align-items: center;
    height: 4em;
    padding: 0 1.375em;
    background: #f6f8fb;
    border-radius: 0 .5em 0 0;
    text-transform: uppercase;
    color: $primary;
    span{
      font-size: .8125em;
    }
    img{
      width: 1.1875em;
      margin-right: .5em;
    }
  }
  &__props{
    display: flex;
  }
  &__prop{
    margin-right: 2.5em;
    &:last-child{
      margin-right: 0;
    }
    &-title{
      font-size: .875em;
      line-height: 179%;
      color: #87878d;
    }
    &-desc{
      font-size: .9375em;
      line-height: 167%;
      font-weight: 600;
    }
  }
  &__add{
    display: flex;
    align-items: center;
    &:hover{
      color: $primary;
      .order-delivery__add-icon{
        background-color: $primary;
        path{
          stroke: #fff;
        }
      }
    }
    &-icon{
      width: 3.375em;
      height: 3.375em;
      background: #f3f3f3;
      @include center;
      border-radius: .3125em;
      transition: all .3s;
      margin-right: 1.5em;
      flex-shrink: 0;
      svg{
        width: 1em;
      }
    }
    &-title{
      font-size: .6875em;
      text-transform: uppercase;
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){
  .order-thank{
    margin-top: 0;
    &__title{
      margin: .9375em 0 .625em;
      padding: 0;
      border: none;
    }
  }

  .order-info{
    padding: .625em 1.5em;
    margin-top: 1.25em;
    &__item-inner{
      gap: .1875em;
    }
  }

  .order-detail{
    &__head{
      display: none;
    }
  }

  .detail-item{
    padding: .625em 1.25em;
    min-height: 2.625em;
    &:first-child{
      border-top: none;
    }
    &__sum{
      font-size: 1.25em;
    }
  }

  .order-delivery{
    margin-top: 1.875em;
    display: flex;
    flex-direction: column;
    &__top{
      padding-left: .9375em;
    }
    &__bottom{
      padding: .8125em .9375em .9375em;
    }
    &__nav{
      display: inline-flex;
      position: static;
      margin-left: auto;
      margin-bottom: -1px;
      margin-right: -1px;
    }
  }
}


@media screen and (max-width: 576px){
  .order-info{
    padding: .625em;
    @include grid100gap(.8125em);
  }

  .detail-item{
    display: block;
    padding-right: .5em;
    &__title2,
    &__desc2{
      font-size: .75em;
      line-height: 200%;
    }
  }

  .order-delivery{
    margin-top: 1.25em;
    &__top{
      flex-direction: column;
    }
    &__name{
      text-align: center;
    }
    &__type{
      order: -1;
      height: 3.1875em;
      margin: 0 auto .8125em;
    }
    &__bottom{
      justify-content: center;
    }
    &__props{
      @include grid100gap(.5625em);
      width: 100%;
    }
    &__prop{
      margin-right: 0;
    }
    &__nav{
      justify-content: space-between;
      margin: 0;
      border-radius: 0;
      border-right: none;
      border-left: none;
      border-bottom: none;
    }
    &__btn{
      padding: .9375em 1.875em;
      margin-right: 0;
      flex-grow: 1;
      justify-content: center;
    }
    &__add{
      margin-top: .4375em;
    }
  }
}
