.pagination{
  margin-top: 3.125em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 3.625em;
    min-width: 3.625em;
    padding: 0 1.25em;
    border-radius: 0.25em;
    border: 1px solid #E9EAEE;
    @include center;
    margin: 0 .3125em;
    font-weight: 500;
    svg{
      width: 1.0625em;
      height: auto;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $primary;
      border-color: $primary;
      path{
        fill: #fff;
      }
    }
    &._btn{
      padding: 0 1.5em;
    }
    &:first-child{
      margin-left: 0;
    }
    &:last-child{
      margin-right: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 1.5625em;
    ._hide-tablet{
      display: none;
    }
    &__item{
      height: 2.5em;
      min-width: 2.5em;
      padding: 0 .5em;
      margin: 0 .25em;
      span{
        font-size: .875em;
      }
      &._btn{
        padding: 0;
        width: 2.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .pagination{

  }
}
