.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: #db2b1f;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.3125em;
    border: 1px solid rgba(46, 48, 54, 0.25);
    padding: 0 1.25em;
    height: 3.6875em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #000;
    font-family: $font1;
    &::placeholder {
      color: #929798;
    }
    &:focus {
      border-color: $primary;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.6875em;
    padding: 0 0 0 1.25em;
    border: 1px solid $light-grey;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &:focus{
      border-color: $light-grey;
    }
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 3.6875em;
      height: 3.6875em;
      background-image: url(../img/arrow4.svg);
      @include bgImgSize(.5em);
      margin-left: 1em;
      border-left: 1px solid $light-grey;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
  }
  &._error{
    input,
    textarea{
      border-color: $red;
      color: $red;
      &::placeholder{
        color: $red;
      }
    }
    label{
      color: $red;
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .25em;
    color: $red;
    display: none;
    font-size: .9375em;
  }
}

.nice-select{
  float: unset;
  .list{
    margin-top: .75em;
    border-radius: .3125em;
    width: auto;
    min-width: 100%;
    border: 1px solid rgba(46, 48, 54, 0.15);
    background: #FFF;
    box-shadow: none;
    max-height: 18em;
    overflow-y: auto;
    z-index: 5;
    @include scroll;
    padding: 1.5em 2em;
  }
  .option{
    height: auto;
    font-size: .875em;
    line-height: 164%;
    padding: 0;
    display: flex;
    align-items: center;
    color: $black;
    border: none;
    margin-bottom: .71em;
    &:last-child{
      border-bottom: none;
      margin-bottom: 0;
    }
    &.disabled{
      display: none;
    }
    &:hover,
    &.focus,
    &.selected.focus{
      background-color: transparent;
      font-weight: 400;
      color: $black;
    }
    &.selected.focus{
      font-weight: 700;
    }
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.5em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.m-size,
.m-color,
.delivery-field,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.5em;
    min-height: 1.5em;
    color: $black;
    span{
      font-size: .9375em;
      line-height: 140%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 1.5em;
      height: 1.5em;
      border: 1px solid #dadedf;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._small{
    label{
      display: block;
      color: #4e5154;
      padding-left: 1.875em;
      min-height: 1.25em;
      padding-top: .0625em;
      &:before{
        width: 1.25em;
        height: 1.25em;
        top: 0;
        transform: unset;
      }
      span{
        font-size: .8125em;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: 84% auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
  &._type2{
    label{
      display: flex;
      align-items: center;
      &:before{
        top: 50%;
        transform: translateY(-50%);
      }
    }
    span{
      font-size: .875em;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 9.625em;
  display: grid;
  grid-template-columns: 3.125em calc(100% - 3.125em * 2) 3.125em;
  height: 3.125em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-bottom: 1px solid #f3f3f3;
    border-top: 1px solid #f3f3f3;
    color: $black;
    font-weight: 600;
    font-size: 1em;
    text-align: center;
  }
  svg{
    width: 1.0625em;
    height: auto;
  }
  button{
    @include center;
    background: #f3f3f3;
    &:hover{
      background-color: #E6E6EC;
    }
  }
  .ccalc-minus{
    border-radius: .3125em 0 0 .3125em;
  }
  .ccalc-plus{
    border-radius: 0 .3125em .3125em 0;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .fg{
    input,
    textarea{
      font-size: .9375em;
    }
  }
}
