.main-slider2{
  margin-top: 1.25em;
  @media screen and (min-width: 993px){
    position: static;
    &__float{
      position: absolute;
      top: 0;
      right: 0;
      width: 21.25em;
    }
    &__float2{
      top: 15.875em;
      margin-top: 1.25em;
    }
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        transform: unset !important;
        position: static;
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
        gap: 1.25em;
      }
      &-slide{
        padding-right: 0;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.main-card{
  height: 15.875em;
  border-radius: .375em;
  position: relative;
  &__img{
    width: 100%;
    height: 100%;
    border-radius: inherit;
    @include imgCover(74.7%);
    padding-bottom: 0;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 1.5em;
  }
  &__content{
    max-width: 10em;
  }
  &__desc{
    color: #75777c;
    margin-top: .3125em;
  }
  &__nav{
    display: flex;
    margin-top: .6875em;
  }
  &__count{
    margin-bottom: .25em;
    color: #8f949b;
  }
}

.main-card2{
  @media screen and (min-width: 993px){
    height: 14.375em;
    border-radius: .5em;
  }
}

.m-section{
  padding: 4.375em 0;
}

.sales-slider{
  margin-top: 2.625em;
  position: relative;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.sale-card{
  position: relative;
  color: $white;
  &._type2{
    .sale-card__content{
      max-width: 100%;
      display: flex;
      justify-content: space-between;
    }
    .sale-card__desc{
      margin: 0 0 0 4em;
      max-width: 10.25em;
    }
  }
  &__img{
    @include imgCover(65.2%);
    border-radius: .3125em;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 2.5em 1.5em;
  }
  &__content{
    max-width: 19.75em;
  }
  &__title{
    font-size: 1.625em;
    line-height: 119%;
    font-family: $font2;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .5625em;
    max-width: 14em;
    font-weight: 700;
  }
  &__link{
    position: absolute;
    right: 1.5625em;
    bottom: 1.5625em;
  }
}

.round-btn{
  box-shadow: -.125em .1875em .4375em rgba(0, 0, 0, 0.1), -.375em .6875em .8125em rgba(0, 0, 0, 0.09), -.875em 1.5625em 1.0625em rgba(0, 0, 0, 0.05), -1.5625em 2.75em 1.25em rgba(0, 0, 0, 0.01), -2.4375em 4.3125em 1.375em rgba(0, 0, 0, 0);
  background-color: $white;
  width: 3.125em;
  height: 3.125em;
  @include center;
  border-radius: 50%;
  &:hover{
    background-color: $primary;
    ._stroke{
      stroke: $white;
    }
  }
  svg{
    width: 1em;
  }
}


@media screen and (max-width: 992px){
  .main-slider2{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .main-card2{
    box-shadow: -.5em .5em 1em rgba(40, 44, 53, 0.04);
  }

  .m-section{
    padding: 2.5em 0;
  }

  .sales{
    overflow: hidden;
  }

  .sales-slider{
    margin-top: 2.5em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 30em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider2{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .m-section{
    padding: 1.875em 0;
  }

  .sales{
    .h2{
      max-width: 85%;
    }
  }

  .sales-slider{
    margin-top: 1.875em;
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        width: 19.5em;
        padding-right: .75em;
      }
    }
  }

  .sale-card{
    &__img{
      padding-bottom: 75%;
    }
    &__content{
      max-width: 100%;
    }
    &__title{
      font-size: .9375em;
    }
    &__desc{
      max-width: 100%;
    }
  }
}
