.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 36.5em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 3.75em;
        border-radius: .625em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        width: 1.5em;
        @include center;
        top: 1.25em;
        right: 1.25em;
        &:hover{
            path{
                stroke: $red;
            }
        }
    }
}

.modal-form{
    margin-top: 2em;
    @include grid100gap(.9375em);
    &__nav{
        margin-top: .9375em;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.map-modal{
    .modal__dialog{
        width: calc(1500px - 5em);
        padding: 1.5625em;
    }
    .map{
        height: 41.5em;
        margin-top: 2em;
    }
    .contact-address{
        margin: 0;
        max-width: 100%;
    }
}

.cookie-modal{
    position: fixed;
    bottom: 0;
    left: 0;
    background: #fff;
    z-index: 21;
    padding: 1.25em 0;
    width: 100%;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0 -.25em 1.5em 0 rgba(0, 0, 0, 0.1);
    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__inner{
        display: flex;
        align-items: center;
    }
    &__icon{
        width: 2.625em;
        margin-right: .9375em;
    }
    &__desc{
        font-size: .875em;
        line-height: 157%;
        color: #383838;
        font-weight: 600;
        a{
            text-decoration: underline;
        }
    }
    &__nav{
        margin-left: auto;
        padding-left: .625em;
    }
    &__btn{
        padding: 0 1.875em;
        height: 2.8125em;
        font-weight: 600;
        text-transform: unset;
        span{
            font-size: .875em;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            width: 29.5em;
            padding: 1.25em;
        }
        .close-btn{
            top: .625em;
            right: .625em;
            width: 1.125em;
        }
    }

    .modal-form{
        margin-top: .9375em;
        &__nav{
            margin-top: 0;
            flex-direction: column;
        }
        &__link{
            margin-top: .875em;
        }
    }

    .map-modal{
        .modal__dialog{
            width: calc(100% - 3.75em);
            padding: 1.25em;
        }
        .map{
            height: 30.5em;
            margin-top: .75em;
        }
    }

    .cookie-modal{
        padding: .9375em 0;
        &__desc{
            line-height: 136%;
        }
        &__btn{
            height: 2.125em;
            padding: 0 2.375em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }

    .map-modal{
        .modal__dialog{
            padding: .625em;
            width: 100%;
        }
        .map{
            height: 22.3125em;
        }
    }

    .cookie-modal{
        &__inner{
            padding-bottom: 2.75em;
            position: relative;
            align-items: flex-start;
        }
        &__nav{
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            padding: 0;
        }
        &__btn{
            width: 100%;
        }
    }
}
