.current-orders{
  margin-top: 2.5em;
  .pagination{
    margin-top: 2.5em;
    &__items{
      justify-content: unset;
    }
  }
  .empty-message{
    margin-top: .625em;
  }
}

.orders-table{
  margin-top: 2.5em;
  border: 1px solid #eaeaea;
  border-radius: .3125em;
  &__grid{
    display: grid;
    grid-template-columns: 18.75em 17.5em calc(100% - 49.125em) 12.875em;
  }
  &__head{
    &-col{
      min-height: 4em;
      display: flex;
      align-items: center;
      font-weight: 700;
      padding-left: 1.25em;
      text-transform: uppercase;
    }
  }
}

.order-row{
  position: relative;
  border--top: 1px solid #eaeaea;
  &:last-child{
    border-bottom: none;
  }
  &__link{
    @include center;
    border-radius: .3125em;
    background-color: #f6f8fb;
    svg{
      width: .3125em;
    }
    &:hover{
      background-color: $primary;
      path{
        stroke: #fff;
      }
    }
    @media screen and (min-width: 993px){
      position: absolute;
      top: 50%;
      right: 1.25em;
      transform: translateY(-50%);
      width: 3.125em;
      height: 3.125em;
      span{
        display: none;
      }
    }
  }
  &__col{
    min-height: 5em;
    display: flex;
    align-items: center;
    padding-left: 1.25em;
    &:last-child{
      padding-right: 5.75em;
    }
  }
  &__title{
    display: none;
  }
  &__price{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 500;
  }
}

.order-status{
  @include center;
  max-width: 100%;
  width: 6.25em;
  height: 2.125em;
  border-radius: .5em;
  text-transform: uppercase;
  border: 1px solid #FB5A00;
  font-weight: 600;
  color: #FB5A00;
  &__round{
    background-color: #ff6915;
    width: 0.5em;
    height: 0.5em;
    flex-shrink: 0;
    border-radius: 50%;
    margin-right: .5em;
  }
  &._red{
    border-color: #F81D2A;
    color: #F81D2A;
    .order-status__round{
      background-color: #F81D2A;
    }
  }
  &._green{
    border-color: #11A342;
    color: #11A342;
    .order-status__round{
      background-color: #11A342;
    }
  }
}

.empty-message{
  padding: 2.1875em;
  @include center;
  color: #35393d;
  background: #f9fafb;
  border-radius: .5em;
  img{
    width: 2.0625em;
    margin-right: .875em;
  }
  a{
    text-decoration: underline;
    color: $primary;
  }
  .text15{
    line-height: 153%;
  }
}

.order-item{
  &__top{
    margin-bottom: 1.875em;
    display: flex;
    justify-content: space-between;
  }
  &__title{
    font-size: 1.3125em;
    line-height: 114%;
    text-transform: uppercase;
    color: #343536;
    font-weight: 700;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1.125em;
  }
  &__date{
    span{
      color: #141414;
    }
  }
  &__status{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      background-color: #d9d9d9;
      border-radius: 50%;
      width: .3125em;
      height: .3125em;
      margin: 0 1.125em;
    }
    .order-status{
      margin-left: 1.125em;
    }
  }
  &__back{
    height: 2.5em;
    @include center;
    padding: 0 2.625em;
    border-radius: .3125em;
    background: #f6f8fb;
    color: $black;
    font-weight: 800;
    text-transform: uppercase;
    &:hover{
      background-color: $black;
      color: #fff;
      path{
        fill: #fff;
      }
    }
    svg{
      margin-right: .625em;
      width: .9375em;
    }
    span{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .current-orders{
    margin-top: 1.5625em;
    .pagination{
      margin-top: .9375em;
      &__items{
        justify-content: center;
      }
    }
  }

  .orders-table{
    margin-top: .9375em;
    border: none;
    &__head{
      display: none;
    }
    &__rows{
      @include grid100gap(.9375em);
    }
  }

  .order-row{
    grid-template-columns: 100%;
    gap: .31em;
    border: 1px solid #EBEBEB !important;
    padding: .94em;
    border-radius: .3125em;
    &__col{
      padding: 0 !important;
      min-height: 1.875em;
      justify-content: space-between;
    }
    &__title{
      display: block;
      font-size: .875em;
      line-height: 130%;
      font-weight: 500;
    }
    &__link{
      height: 2.375em;
      text-transform: uppercase;
      font-weight: 700;
      color: $primary;
      span{
        font-size: 0.75em;
        line-height: 130%;
        letter-spacing: 0.01em;
      }
      svg{
        margin-left: .375em;
      }
    }
  }

  .empty-message{
    padding: .9375em;
  }

  .order-item{
    &__top{
      margin-bottom: 1.5em;
    }
    &__title{
      font-size: 1.0625em;
    }
    &__back{
      display: none;
    }
    &__info{
      margin-top: .4375em;
    }
    &__status{
      &:before{
        margin: 0 .6875em;
      }
      .order-status{
        margin-left: .5625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .current-orders{
    margin-top: 1.25em;
    .pagination{
      margin-top: 1.25em;
    }
  }

  .orders-table{
    margin-top: 1.25em;
  }

  .empty-message{
    align-items: flex-start;
  }

  .order-item{
    &__top{
      margin-bottom: 1.25em;
    }
    &__info{
      display: block;
    }
    &__status{
      margin: .4375em 0 0;
      &:before{
        display: none;
      }
    }
  }
}
