.blog{
  padding-bottom: 5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 22.5em) 21.25em;
    gap: 1.5625em 1.25em;
  }
}

.blog-card{
  &__img{
    @include imgCover(69.7%);
    border-radius: .3125em;
  }
  &__content{
    margin-top: 1.25em;
  }
  &__date{
    font-size: .875em;
    line-height: 107%;
    font-weight: 600;
    color: #8f949b;
  }
  &__top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: .5em 0;
    svg{
      width: 1.5em;
      margin: .25em 0 0 1em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__title{
    font-size: 1.1875em;
    line-height: 116%;
    font-weight: 700;
    @include truncate(2);
    min-height: 2.32em;
  }
  &__desc{
    color: #78797c;
    font-weight: 600;
  }
}

.news-nav{
  position: sticky;
  top: 1em;
  &__show{
    display: none;
  }
  &__link{
    padding: 1em 1.875em;
    display: flex;
    align-items: center;
    min-height: 3.5em;
    background-color: $light-grey;
    &:first-child{
      border-top-left-radius: .1875em;
      border-top-right-radius: .1875em;
    }
    &:last-child{
      border-bottom-left-radius: .1875em;
      border-bottom-right-radius: .1875em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover,
    &._active{
      background-color: $primary;
      color: #fff;
    }
    &._active{
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding-bottom: 3.125em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      order: -1;
    }
  }

  .blog-card{
    &__content{
      margin-top: .625em;
    }
    &__date{
      font-size: .75em;
      line-height: 125%;
    }
    &__top{
      svg{
        display: none;
      }
    }
    &__title{
      font-size: .9375em;
    }
  }

  .news-nav{
    top: unset;
    width: calc(50% - .625em);
    position: relative;
    &__show{
      cursor: pointer;
      display: flex;
      align-items: center;
      height: 3.75em;
      border-radius: .1875em;
      border: 1px solid $light-grey;
      padding: 0 1.75em 0 1.25em;
      justify-content: space-between;
      position: relative;
      &:after{
        content: '';
        position: absolute;
        top: 0;
        right: 3.75em;
        width: 1px;
        height: 100%;
        background-color: $light-grey;
      }
      svg{
        width: .5em;
        height: auto;
        margin-left: 1.75em;
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      border-radius: .1875em;
      overflow: hidden;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      background-color: #fff;
      border: 1px solid $light-grey;
      margin-top: .125em;
    }
    &__link{
      border-radius: 0 !important;
      min-height: 2.8125em;
      padding: .75em 1.25em;
      background-color: transparent;
      span{
        font-size: .875em;
      }
      &._active{
        display: none;
      }
    }
    &._show{
      .news-nav__show{
        svg{
          transform: rotate(-180deg);
        }
      }
      .news-nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog-card{
    &__title{
      min-height: unset;
    }
  }

  .news-nav{
    width: 100%;
  }
}
