.contacts{
  padding-bottom: 5em;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
    gap: 1.875em;
  }
}

.contact-item{
  border: 1px solid #dfdfdf;
  border-radius: .3125em;
  padding: 2.5em;
  &__title{
    font-size: 1em;
    line-height: 140%;
    font-weight: 600;
  }
  &__email{
    display: flex;
    align-items: center;
    margin-top: .5em;
    img{
      width: 1.3125em;
      margin-right: .4375em;
    }
    &-link{
      font-size: .9375em;
      line-height: 140%;
      text-decoration: underline;
      color: $primary;
    }
  }
  .socials{
    margin-top: .5em;
  }
}

.contact-phone{
  display: flex;
  align-items: flex-start;
  margin-top: .5em;
  img{
    width: 1em;
    margin-right: .375em;
    margin-top: .1875em;
  }
  &__link{
    font-size: .9375em;
    line-height: 140%;
  }
  &__desc{
    margin-top: .5em;
  }
}

.contact-address{
  margin-top: .5em;
  display: flex;
  align-items: flex-start;
  max-width: 16.75em;
  img{
    width: 1.3125em;
    margin-right: .4375em;
  }
  &__street{
    color: #0c2227;
  }
  &__desc{
    margin-top: .5em;
    color: #404748;
  }
}

.contact-time{
  color: #404748;
  margin-top: .5em;
  @include grid100gap(.5em);
  max-width: 12em;
}

.contact-requisites{
  color: #404748;
  margin-top: .5em;
  @include grid100gap(.5em);
  span{
    font-weight: 600;
    color: #111417;
  }
}

.contacts-form{
  margin-top: 3.75em;
  &__fields{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
    gap: 1.875em;
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 4.5em;
    margin-right: 1.875em;
  }
}

._full{
  grid-column: 1/-1;
}

.map{
  margin-top: 3.125em;
  height: 31.75em;
  border-radius: .3125em;
  overflow: hidden;
}

.m-form{
  position: relative;
  &._success{
    .m-form__inner{
      opacity: 0;
      pointer-events: none;
    }
    .thank-form{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.thank-form{
  position: absolute;
  @include center;
  flex-direction: column;
  text-align: center;
  padding: 1em;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-radius: .3125em;
  opacity: 0;
  pointer-events: none;
  &__img{
    width: 4.625em;
    display: block;
    margin-bottom: .94em;
  }
  &__text{
    margin-top: .94em;
    max-width: 33.0625em;
    color: #363a3a;
    .text17{
      line-height: 164%;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-bottom: 3.125em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .contact-item{
    padding: 1.25em;
  }

  .contact-phone{
    &__desc{
      margin-top: -.25em;
      pointer-events: none;
    }
  }

  .contacts-form{
    margin-top: 1.5625em;
    .h3{
      text-align: center;
    }
    &__fields{
      margin-top: .625em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: .625em 1.25em;
    }
    &__nav{
      margin-top: .625em;
      display: block;
    }
    &__btn{
      margin: 0 auto .9375em;
    }
  }

  .map{
    height: 21.6875em;
    margin-top: 1.5625em;
  }

  .thank-form{
    &__text{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__items{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .contacts-form{
    margin-top: 1.25em;
    .h3{
      max-width: 80%;
      margin: 0 auto;
    }
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .map{
    height: 31.75em;
    margin-top: 1.25em;
  }
}
