@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5625em;
    height: 3.375em;
    border-radius: .3125em;
    font-weight: 800;
    text-transform: uppercase;
    span{
        font-size: .75em;
    }
    &:focus {
        box-shadow: none;
    }
}

.m-btn-small{
    padding: 0 .8125em;
    height: 2.125em;
    span{
        font-size: .6875em;
    }
}

.m-btn-arrow{
    img,
    svg{
        width: .875em;
        margin-left: .5em;
    }
}

.text-btn{
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.22);
    color: #fff;
    &:hover{
        color: #fff;
        opacity: .8;
    }
    span{
        font-size: .875em;
        line-height: 150%;
    }
}

.m-btn-blue-outline,
.m-btn-white,
.m-btn-orange,
.m-btn-black,
.m-btn-blue{
    &:disabled{
        border-color: #E0E0E0;
        background-color: #E0E0E0;
        color: #9ca1ac;
        box-shadow: none;
        ._stroke{
            stroke: #9ca1ac;
        }
    }
}

.m-btn-orange{
    background-color: $orange;
    color: $white;
    box-shadow: $shadow;
    &:hover{
        background-color: $black;
        color: $white;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white;
    box-shadow: $shadow2;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    box-shadow: 0 .25em .25em rgba(40, 44, 53, 0.04), 0 .625em 1.0625em 0 rgba(55, 107, 204, 0.19);

    &:hover{
        background-color: $orange;
        color: $white;
    }
}

.m-btn-light-blue{
    background-color: rgba($primary,.1);
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: $orange;
        color: $white;
        ._stroke{
            stroke: $white;
        }
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
        ._stroke{
            stroke: $white;
        }
    }
}

.m-btn-text{
    height: unset;
    padding: 0;
    color: $primary;
    &:hover{
        color: $black;
    }
}

.m-btn-transparent{
    color: $primary;
    &:hover{
        color: $black;
    }
}
