.catalog-top{
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.875em;
}

.selected-filters{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.75em;
}

.selected-filter{
  display: flex;
  align-items: center;
  margin: .75em .9375em 0 0;
  background: #f8f8f8;
  padding: 0 .625em;
  height: 2.5em;
  border-radius: .3125em;
  &__remove{
    width: 1em;
    @include center;
    flex-shrink: 0;
    margin-right: .3125em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &--clear{
    background: rgba(55, 107, 204, 0.05);
    color: $primary;
    &:hover{
      background: rgba(55, 107, 204, 0.15);
    }
    &__remove{
      pointer-events: none;
    }
    path{
      stroke: $primary;
    }
  }
}

.sort{
  display: flex;
  align-items: center;
  &__select{
    margin-left: .9375em;
  }
  .nice-select{
    padding: 0;
    border: none;
    height: 2.5em;
    display: flex;
    align-items: center;
    font-size: 1em;
    width: auto;
    &:before{
      content: '';
      order: 2;
      flex-shrink: 0;
      width: 1.125em;
      height: 1.125em;
      background-image: url(../img/arrow3.svg);
      @include bgImgSize(100%);
      margin-left: .3125em;
    }
    .list{
      left: unset;
      right: 0;
    }
    .current{
      font-size: .875em;
      line-height: 164%;
      font-weight: 600;
    }
  }
}

.catalog-items{
  row-gap: 3.75em;
}

.catalog-bottom{
  margin-top: 4em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .pagination{
    margin-top: 0;
  }
  &__btn{
    padding: 0 3.75em;
    height: 3.625em;
  }
}

.favourite{
  .product-card__action{
    opacity: 1;
  }
  &__bottom{
    margin-top: 5.625em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    padding: 0 3.75em;
  }
}

.pin{
  height: 2.5em;
  min-width: 2.5em;
  padding: 0 .25em;
  background-color: #ED9C62;
  border-radius: 2.25em;
  color: #fff;
  font-weight: 700;
  @include center;
  span{
    font-size: .875em;
  }
}

.category-menu,
.category-item{
  &__more{
    margin-top: .375em;
    font-weight: 600;
    color: $primary;
    ._view2{
      display: none;
    }
  }
}

.category-menu{
  &__items{
    @include grid100gap(.625em);
  }
  &__more{
    margin-top: .625em;
  }
  &._show-all-links{
    .category-menu__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  @media screen and (min-width: 993px){
    &:not(._show-all-links){
      .category-item:nth-child(4)~.category-item{
        display: none;
      }
    }
    &__back{
      display: none;
    }
  }
}

.category-item{
  &__link,
  &__title{
    font-size: .875em;
    line-height: 172%;
  }
  &__title{
    font-weight: 600;
    span{
      opacity: .5;
      font-weight: 400;
    }
  }
  &__hidden{
    padding: .625em 0 0 .625em;
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
  }
  &__link{
    margin-top: .43em;
    &:first-child{
      margin-top: 0;
    }
  }
  &._show-all-links{
    .category-item__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  @media screen and (min-width: 993px){
    &._active{
      .category-item__title{
        font-weight: 700;
        color: $primary;
      }
      .category-item__hidden{
        display: block;
      }
    }
    &:not(._show-all-links){
      .category-item__link:nth-child(5)~.category-item__link{
        display: none;
      }
    }
  }
}

.filter-group{
  &._category-mob{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .catalog-top{
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 2.5em;
  }

  .selected-filters{
    order: 2;
    width: 100%;
    margin-top: 1em;
  }

  .selected-filter{
    margin: .3125em .3125em 0 0;
  }

  .filter-btn{
    display: flex;
    height: 3em;
    padding: 0 2.5em;
    text-transform: unset;
    &:hover{
      path{
        stroke: $white;
      }
    }
    span{
      font-size: 1em;
    }
    svg{
      margin-right: .625em;
    }
  }

  .catalog-items{
    row-gap: 2.1875em;
  }

  .catalog-bottom{
    margin-top: 3.125em;
    justify-content: center;
    &__btn{
      display: none;
    }
  }

  .search{
    .empty-message{
      margin-top: 0;
    }
  }

  .favourite{
    &__bottom{
      margin-top: 1.1875em;
    }
  }

  .pin{
    height: 1.75em;
    min-width: 1.75em;
    border-radius: 1.75em;
  }

  .category-menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    z-index: 3;
    padding: 3.125em;
    overflow-y: auto;
    &__more{
      display: none;
    }
    &__back{
      display: flex;
      align-items: center;
      font-weight: 800;
      margin-bottom: 1.25em;
      svg{
        width: 1.125em;
        margin-right: .3125em;
      }
      span{
        font-size: 1em;
        line-height: 150%;
      }
    }
    &__items{
      padding-left: 1.375em;
    }
  }

  .category-item{
    &._open{
      .category-item__show:after{
        transform: rotate(-180deg);
      }
    }
    &__show{
      display: flex;
      align-items: center;
      &:after{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/arrow5.svg);
        @include bgImgSize(100%);
        transition: all .3s;
        margin-left: .5em;
        width: .5em;
        height: .5em;
      }
    }
    &__title{
      span{
        display: none;
      }
    }
    &__more{
      display: none;
    }
  }

  .filter-group{
    &._category{
      padding-bottom: 0;
      margin-bottom: 0;
      border: none;
      .filter-group__bottom{
        position: static;
        display: block !important;
      }
      .filter-group__top{
        display: none;
      }
    }
    &._category-mob{
      display: flex;
      align-items: center;
      justify-content: space-between;
      svg{
        width: 1.125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-top{
    margin-bottom: 1.25em;
  }

  .filter-btn{
    width: 100%;
  }

  .sort{
    margin-top: .75em;
  }

  .selected-filters{
    margin-top: .5em;
  }

  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
    gap: .9375em;
  }

  .favourite{
    &__bottom{
      margin-top: .9375em;
    }
  }

  .category-menu{
    padding: 1.5625em;
  }
}
