.catalog-item{
  padding-bottom: 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: 41em calc(100% - 43.5em);
    gap: 2.5em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 5.375em calc(100% - 6em);
  gap: .625em;
  position: relative;
  .swiper{
    height: 100%;
  }
  &__small,
  &__big{
    height: 43.125em;
    .swiper {
      height: calc(100% + .5625em);
      &-slide{
        padding-bottom: .5625em;
      }
    }
  }
  &__small{
    .swiper{
      height: calc(100% + .5625em);
      &-slide{
        height: calc(100% / 6);
        padding-bottom: .5625em;
        cursor: pointer;
        &-thumb-active{
          .gallery__img{
            &:after{
              opacity: 1;
            }
          }
        }
      }
    }
    .gallery__img{
      pointer-events: none;
      &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.2);
        border-radius: inherit;
        opacity: 0;
      }
    }
  }
  &__img{
    @include imgCover(123.2%);
    padding: 0;
    height: 100%;
    border-radius: .5em;
  }
  &__nav{
    left: 0;
    bottom: 0;
    //position: absolute;
    //height: 6.6875em;
    //width: 5.375em;
    //@include center;
  }
  .swiper-button{
    z-index: 3;
    //position: static;
    transform: rotate(90deg);
    margin: 0;
    left: 1.5625em;
    &:hover{
      border-color: $primary;
    }
    &-prev{
      //display: none;
      top: 2.211875em;
    }
    &-next{
      top: unset;
      bottom: 2.211875em;
    }
  }
  &__badges{
    position: absolute;
    display: flex;
    top: .625em;
    left: 6.625em;
    z-index: 2;
    .m-badge{
      height: 1.875em;
      margin-right: .375em;
    }
  }
}

.product-info{
  @include grid100gap(1.25em);
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .rating-input{
    &__item{
      width: 1.25em;
    }
  }
  .product-card__reviews{
    margin: 0;
    &-count{
      color: #808080;
    }
  }
  &__articul{
    opacity: .7;
  }
  &__desc{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .text16{
      @include truncate(2);
    }
    &-link{
      margin-top: .625em;
      font-size: 1em;
      line-height: 156%;
      border-bottom: 1px dashed rgba($primary,.4);
      color: $primary;
      &:hover{
        border-color: $primary;
      }
    }
  }
  .empty-message{
    margin-top: .25em;
    justify-content: unset;
    padding: .9375em 1.25em;
    border: 1px dashed #b9d1ea;
    img{
      width: 1.5em;
    }
  }
}

.product-colors{
  margin-top: .75em;
  display: flex;
  align-items: center;
  &__items{
    margin-left: 1.25em;
    display: flex;
  }
  .m-color{
    padding-left: 1.5em;
    margin-right: .625em;
  }
}

.product-sizes{
  margin-top: .625em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title,
  &__btn{
    font-size: 1em;
    line-height: 156%;
    color: #45464a;
  }
  &__btn{
    text-decoration: underline;
  }
  &__items{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(9,calc(100% / 9 - 5em / 9));
    gap: .625em;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: 1.25em;
    &__grid{
      grid-template-columns: 21.4375em calc(100% - 22.875em);
      gap: 1.4375em;
    }
  }

  .gallery{
    grid-template-columns: 100%;
    &__small,
    &__big{
      height: auto;
    }
    .swiper{
      height: 100%;
      width: calc(100% + .375em);
      &-slide{
        height: auto;
        padding: 0 .375em 0 0;
      }
      &-wrapper{
        height: auto;
      }
    }
    &__small{
      order: 2;
      .swiper-slide{
        width: 20%;
      }
    }
    &__img{
      height: auto;
      padding-bottom: 123%;
    }
    &__nav{
      position: static;
      height: auto;
    }
    &__badges{
      top: .625em;
      left: .625em;
    }
    .swiper-button{
      position: absolute;
      left: 1.25em;
      right: 1.25em;
      bottom: 1.6875em;
      top: unset;
      transform: unset;
      width: 1.375em;
      height: 1.375em;
      &-next{
        left: unset;
      }
    }
  }

  .product-info{
    gap: .625em;
    &__wrap{
      @include grid100gap(.75em);
    }
    .empty-message{
      margin-top: .875em;
      padding-right: 1.875em;
    }
  }

  .product-colors{
    margin-top: .625em;
    &__items{
      margin-left: 1em;
    }
  }

  .product-sizes{
    &__title,
    &__btn{
      font-size: .875em;
    }
    &__items{
      margin-top: .625em;
      grid-template-columns: repeat(5,calc(20% - 2.5em / 5));
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
      gap: 2.1875em;
    }
  }

  .gallery{
    &__small{
      display: none;
    }
    &__img{
      padding-bottom: 128%;
    }
    &__nav{
      position: absolute;
      left: unset;
      right: .875em;
      bottom: .875em;
      width: auto;
      display: flex;
      align-items: center;
    }
    .swiper-button{
      position: static;
      margin: 0 0 0 .4375em;
      &-prev{
        display: flex;
      }
    }
  }

  .product-info{
    gap: 1.25em;
    .empty-message{
      margin-top: .25em;
    }
  }

  .product-colors{
    margin-top: 1.25em;
  }

  .product-sizes{
    margin-top: 1.25em;
  }
}
